import { UserService } from '../domain/user/user.service';
import { UserRepository } from '../domain/user/user.repository';
import { UserStatus, User } from '@domain/user/user.model';
import { CustomError } from '@common/models/CustomError';
import { HttpErrCode } from '@common/constants/HttpErrCode';
import { encrypt } from '@utils/authenticator';
import { mailer } from '@utils/mailer';
import { genRandomNumber } from '@utils/helper';
import { WalletService } from '@domain/wallet/wallet.service';
import { PocketRepository } from '@domain/pocket/pocket.repository';
import { Pocket, SelectedColumn } from '@domain/pocket/pocket.model';

export class UserApi {
  constructor(
    private userService: UserService,
    private userRepository: UserRepository,
    private walletService: WalletService,
    private pocketRepository: PocketRepository
  ) {
    this.userService = userService;
    this.userRepository = userRepository;
    this.walletService = walletService;
    this.pocketRepository = pocketRepository;
  }

  async register(email, name, password, phone, birthday) {
    let users = await this.userRepository.findAll({
      where: { email, status: UserStatus.ACTIVE }
    });
    if (users.length !== 0)
      throw new CustomError(
        HttpErrCode.REGISTER.EXIST_EMAIL,
        '  .'
      );
    users = await this.userRepository.findAll({
      where: { phone, status: UserStatus.ACTIVE }
    });
    if (users.length !== 0)
      throw new CustomError(
        HttpErrCode.REGISTER.EXIST_PHONE,
        '   .'
      );

    let code = genRandomNumber(6);
    let user = new User({
      email,
      password: encrypt(password),
      emailVerificationCode: code,
      phone,
      birthday
    });

    let created = await this.userRepository.save(user);
    console.log('created!', created.id);
    let wallet = await this.walletService.popWallet();
    let pocket = new Pocket();
    pocket.userId = created.id;
    pocket.walletId = wallet.id;
    pocket.selectedColumn = SelectedColumn.walletId;

    await this.pocketRepository.save(pocket);
    mailer.send(
      { name, email },
      '  .',
      `<html>
        <a href="http://localhost:3001/api/v1/auth/verify-email?email=${email}&code=${code}"></a>
      </html>`
    );

    return created;
  }
}
