import {
  Table,
  Model,
  Column,
  DataType,
  PrimaryKey,
  AutoIncrement,
  ForeignKey,
  AllowNull,
  CreatedAt
} from 'sequelize-typescript';
import { User } from '@domain/user/user.model';
import { ExternalWallet } from '@domain/external-wallet/external-wallet.model';
import { Wallet } from '@domain/wallet/wallet.model';
export enum SelectedColumn {
  externalWalletId = 'externalWalletId',
  walletId = 'walletId'
}
/**
 * @swagger
 * definitions:
 *   Sample:
 *     type: object
 *     required:
 *       - id
 *     properties:
 *       id:
 *         type: string
 */
@Table({ modelName: 'pocket' })
export class Pocket extends Model<Pocket> {
  @PrimaryKey
  @AutoIncrement
  @Column({ type: DataType.INTEGER })
  public id: number = 0;

  @AllowNull(false)
  @Column({ type: DataType.ENUM({ values: Object.keys(SelectedColumn) }) })
  selectedColumn: SelectedColumn;

  @AllowNull(false)
  @ForeignKey(() => User)
  @Column({ type: DataType.INTEGER })
  public userId: number = 0;

  @ForeignKey(() => ExternalWallet)
  @Column({ type: DataType.INTEGER })
  public externalWalletId: number = 0;

  @ForeignKey(() => Wallet)
  @Column({ type: DataType.INTEGER })
  public walletId: number = 0;

  @CreatedAt
  createdAt: Date;
}
