import { Route } from 'server/common/models/Route';
import * as express from 'express';
import { httpSuccessResponse } from '@utils/httpSender';
import { TransactionRepository } from '@domain/transaction/transaction.repository';

export class TransactionRoute implements Route {
  private router: express.Router;
  constructor(private transactionRepository: TransactionRepository) {
    this.transactionRepository = transactionRepository;
    this.router = express.Router();
  }
  handle() {
    this.router.post('', (...args) => this.create(...args));
    this.router.get('', (...args) => this.findAll(...args));
    this.router.get('/:id', (...args) => this.findById(...args));
    this.router.put('', (...args) => this.update(...args));
    this.router.delete('', (...args) => this.remove(...args));
    return this.router;
  }

  private async create(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let transaction = req.body;

      let created = await this.transactionRepository.save(transaction);
      return res.send(created);
    } catch (err) {
      return next(err);
    }
  }

  // @authGuard
  private async findAll(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      console.log(req.query);
      let payload = await this.transactionRepository.findAll(req.query);
      return httpSuccessResponse(res, { payload });
    } catch (err) {
      return next(err);
    }
  }

  private async findById(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let id = req.params.id;
      let transaction = await this.transactionRepository.findById(id);

      return res.send(transaction);
    } catch (err) {
      return next(err);
    }
  }

  private async update(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let { transaction, option } = req.body;
      let [count, payload] = await this.transactionRepository.update(
        transaction,
        option
      );

      return httpSuccessResponse(res, { payload, count });
    } catch (err) {
      return next(err);
    }
  }

  private async remove(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let option = req.body;
      let count = await this.transactionRepository.delete(option);

      res.send(count.toString());
    } catch (err) {
      return next(err);
    }
  }
}
