import { Route } from 'server/common/models/Route';
import * as express from 'express';
import { httpSuccessResponse } from '@utils/httpSender';
import { RewardRepository } from '@domain/reward/reward.repository';
import Joi from 'joi';
import { FilterSchema } from '@common/validateSchemas/FilterSchema';

export class RewardRoute implements Route {
  private router: express.Router;
  constructor(private rewardRepository: RewardRepository) {
    this.rewardRepository = rewardRepository;
    this.router = express.Router();
  }
  handle() {
    /**
     * @swagger
     * /rewards:
     *   post:
     *     description: Reward API
     *     tags:
     *       - Rewards
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: body
     *         name: body
     *         schema:
     *           $ref: '#/definitions/CreateRewardSchema'
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           $ref: '#/definitions/Reward'
     */
    this.router.post('', (...args) => this.create(...args));
    /**
     * @swagger
     * /rewards:
     *   get:
     *     description: Reward API
     *     tags:
     *       - Rewards
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: query
     *         name: where
     *         type: object
     *         description: Where clause explains about the constraints used in find data
     *       - in: query
     *         name: limit
     *         type: integer
     *         description: How many data do you want?
     *       - in: query
     *         name: offset
     *         type: integer
     *         description: Where to start find data
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           type: object
     *           properties:
     *             payload:
     *               type: array
     *               items:
     *                 $ref: '#/definitions/Reward'
     */
    this.router.get('', (...args) => this.findAll(...args));
    /**
     * @swagger
     * /rewards/{id}:
     *   get:
     *     description: Reward API
     *     tags:
     *       - Rewards
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: path
     *         name: id
     *         type: number
     *         description: Single reward
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           $ref: '#/definitions/Reward'
     */
    this.router.get('/:id', (...args) => this.findById(...args));
    /**
     * @swagger
     * /rewards:
     *   put:
     *     description: Update reward
     *     tags:
     *       - Rewards
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: body
     *         name: body
     *         schema:
     *           properties:
     *             data:
     *               $ref: '#/definitions/Reward'
     *             option:
     *               type: object
     *               schema:
     *                 type: object
     *                 properties:
     *                   where:
     *                     type: string
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           $ref: '#/definitions/Reward'
     */
    this.router.put('', (...args) => this.update(...args));
    /**
     * @swagger
     * /rewards:
     *   delete:
     *     description: Delete reward
     *     tags:
     *       - Rewards
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: body
     *         name: body
     *         schema:
     *           $ref: '#/definitions/Reward'
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           properties:
     *             count:
     *               type: integer
     */
    this.router.delete('', (...args) => this.remove(...args));
    return this.router;
  }

  private async create(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let reward = req.body;

      let created = await this.rewardRepository.save(reward);
      return res.send(created);
    } catch (err) {
      return next(err);
    }
  }

  // @authGuard
  private async findAll(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      if (req.query.where && typeof req.query.where.userId === 'string')
        req.query.where.userId = parseInt(req.query.where.userId);

      let { value, error } = Joi.validate(req.query, FilterSchema, {
        convert: true
      });

      if (error) return next(error);

      let rewards = await this.rewardRepository.findAll(value);
      let totalCount = await this.rewardRepository.getCount(value);
      return httpSuccessResponse(res, { payload: rewards, totalCount });
    } catch (err) {
      return next(err);
    }
  }

  private async findById(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let id = req.params.id;
      let reward = await this.rewardRepository.findById(id);

      return res.send(reward);
    } catch (err) {
      return next(err);
    }
  }

  private async update(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let { data, option } = req.body;
      let [count, rewardList] = await this.rewardRepository.update(
        data,
        option
      );

      let payload = rewardList;
      return httpSuccessResponse(res, { payload, count });
    } catch (err) {
      return next(err);
    }
  }

  private async remove(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let option = req.body;
      let count = await this.rewardRepository.delete(option);

      res.send(count.toString());
    } catch (err) {
      return next(err);
    }
  }
}
