import Joi from 'joi';
import { OrderStatus } from '../../domain/order/order.model';
import { Currency } from '../models/Currency';
export const CreateOrderSchema = Joi.object().keys({
  userId: Joi.number()
    .required()
    .positive(),
  salesId: Joi.number()
    .positive()
    .required(),
  tokenId: Joi.number()
    .required()
    .positive(),
  status: Joi.string()
    .valid(Object.keys(OrderStatus))
    .required(),
  hash: Joi.string().required(),
  purchasePrice: Joi.number()
    .positive()
    .required(),
  purchaseCurrency: Joi.string()
    .valid(Object.keys(Currency))
    .required(),
  tokenAmount: Joi.number()
    .positive()
    .required()
});
