
  import { Wallet } from '@domain/wallet/wallet.model';
  import { DestroyOptions, UpdateOptions } from 'sequelize';
  import { Filter } from '@common/models/QueryOption';
  import { ICountOptions } from 'sequelize-typescript';
  import { removeDotInJson } from '@frontalnh/json-dot-parser';

  export class WalletRepositoryImpl {
    constructor() {}
  
    async save(wallet: Wallet) {
      return await wallet.save();
    }
  
    async findAll(filter: Filter) {
      if (!filter.raw) return await Wallet.findAll(filter);

      let datas = await Wallet.findAll(filter);
      for (let data of datas) {
        Object.assign(data, removeDotInJson(data));
      }
  
      return datas;
    }
  
    async findById(id: number) {
      return await Wallet.findByPrimary(id);
    }

    async getCount(filter: ICountOptions<Wallet>) {
      return await Wallet.count(filter);
    }
  
    async update(
      wallet: Partial<Wallet>,
      option: UpdateOptions
    ): Promise<[number, Wallet[]]> {
      return await Wallet.update(wallet, option);
    }
  
    async delete(option: DestroyOptions): Promise<number> {
      return await Wallet.destroy(option);
    }
  }
  