import { HttpErrCode } from '../common/constants/HttpErrCode';
import { UserRepository } from '../domain/user/user.repository';
import { encodeJwt } from '@utils/authenticator';
import { User, UserStatus } from '@domain/user/user.model';
import { CustomError } from '@common/models/CustomError';

export class AuthApi {
  userRepository: UserRepository;

  constructor(userRepository: UserRepository) {
    this.userRepository = userRepository;
  }

  public async authenticateEmail(email, code) {
    let users = await this.userRepository.findAll({
      where: {
        email,
        status: UserStatus.UNAUTHORIZED
      },
      order: [['createdAt', 'DESC']]
    });

    if (users.length === 0)
      throw new CustomError(
        HttpErrCode.VERIFY_EMAIL.NO_USER,
        '  .'
      );

    let user = users[0];

    user.status = UserStatus.ACTIVE;
    await this.userRepository.save(user);
    if (user.emailVerificationCode === code) return true;

    throw new CustomError(HttpErrCode.VERIFY_EMAIL.INVALID_CODE);
  }

  async login(email, password): Promise<{ user: User; token: string }> {
    let users = await this.userRepository.findAll({
      where: { email, status: UserStatus.ACTIVE }
    });

    if (users.length == 0) {
      throw new CustomError(HttpErrCode.LOGIN.NO_USER);
    }

    let user = users[0];

    if (!user.comparePassword(password)) {
      throw new CustomError(
        HttpErrCode.LOGIN.INVALID_PASSWORD,
        '  .'
      );
    }

    const jwtToken = encodeJwt({ user: { id: user.id } });

    return { user, token: jwtToken };
  }
}
