// require('ts-node').register();

export enum DBType {
  SEQUELIZE = 'SEQUELIZE',
  MONGODB = 'MONGODB'
}

export class Components {
  public appliedCouponRepository: AppliedCouponRepository;
  public assetDescRepository: AssetDescRepository;
  public assetImageRepository: AssetImageRepository;
  public assetRepository: AssetRepository;
  public categoryRepository: CategoryRepository;
  public couponRepository: CouponRepository;
  public ethereumConfigRepository: EthereumConfigRepository;
  public exchangeRateRepository: ExchangeRateRepository;
  public externalWalletRepository: ExternalWalletRepository;
  public orderRepository: OrderRepository;
  public rewardRepository: RewardRepository;
  public salesProposalRepository: SalesProposalRepository;
  public salesRepository: SalesRepository;
  public tokenRepository: TokenRepository;
  public transactionRepository: TransactionRepository;
  public userRepository: UserRepository;
  public walletRepository: WalletRepository;
  public monthlyAssetInfoRepository: MonthlyAssetInfoRepository;
  public pocketRepository: PocketRepository;
  public connectedAccountRepository: ConnectedAccountRepository;
  public apiUserRepository: ApiUserRepository;

  public userService: UserService;
  public pocketService: PocketService;
  public walletService: WalletService;
  public monthlyAssetInfoService: MonthlyAssetInfoService;
  public connectedAccountService: ConnectedAccountService;

  public assetApi: AssetApi;
  public userApi: UserApi;
  public authApi: AuthApi;

  constructor() {
    // repositories
    this.appliedCouponRepository = new AppliedCouponRepositoryImpl();
    this.assetDescRepository = new AssetDescRepositoryImpl();
    this.assetImageRepository = new AssetImageRepositoryImpl();
    this.assetRepository = new AssetRepositoryImpl();
    this.categoryRepository = new CategoryRepositoryImpl();
    this.couponRepository = new CouponRepositoryImpl();
    this.ethereumConfigRepository = new EthereumConfigRepositoryImpl();
    this.exchangeRateRepository = new ExchangeRateRepositoryImpl();
    this.externalWalletRepository = new ExternalWalletRepositoryImpl();
    this.orderRepository = new OrderRepositoryImpl();
    this.rewardRepository = new RewardRepositoryImpl();
    this.salesProposalRepository = new SalesProposalRepositoryImpl();
    this.salesRepository = new SalesRepositoryImpl();
    this.tokenRepository = new TokenRepositoryImpl();
    this.transactionRepository = new TransactionRepositoryImpl();
    this.userRepository = new UserRepositoryImpl();
    this.walletRepository = new WalletRepositoryImpl();
    this.monthlyAssetInfoRepository = new MonthlyAssetInfoRepositoryImpl();
    this.pocketRepository = new PocketRepositoryImpl();
    this.connectedAccountRepository = new ConnectedAccountRepositoryImpl();
    this.apiUserRepository = new ApiUserRepositoryImpl();

    // services
    this.walletService = new WalletService(this.walletRepository);
    this.connectedAccountService = new ConnectedAccountService(
      this.connectedAccountRepository
    );
    this.pocketService = new PocketService(this.pocketRepository);
    this.userService = new UserService(
      this.userRepository,
      this.pocketService,
      this.walletService
    );
    this.monthlyAssetInfoService = new MonthlyAssetInfoService(
      this.monthlyAssetInfoRepository
    );

    // apis
    this.userApi = new UserApi(
      this.userService,
      this.userRepository,
      this.walletService,
      this.pocketRepository
    );
    this.assetApi = new AssetApi(
      this.assetRepository,
      this.transactionRepository,
      this.monthlyAssetInfoService
    );
    this.authApi = new AuthApi(this.userRepository);
  }
}
require('ts-node').register();

export class App {
  public async start() {
    const app = express();

    // post body   body parser
    app.use(bodyParser.urlencoded({ limit: '50mb', extended: false }));
    app.use(bodyParser.json({ limit: '50mb' }));

    //   .
    app.use(cookieParser());

    app.use(headerSetter);
    app.use(compression());
    app.use(passport.initialize());

    // allow cors to sdf.splashpay.net for all routes
    app.use(cors(this.getCorsOption()));

    // sendgrid setting
    this.setSendgrid();
    await this.initDB(DBType.SEQUELIZE);

    require('dotenv').config();

    // swagger settings start
    this.generateSwaggerDefinitions();
    app.use('/api-docs', swaggerUi.serve, swaggerUi.setup(swaggerSpec));
    // swagger settings end

    // ddd component setting start
    const components = new Components();
    app.use('/', router(components));
    // ddd component setting end

    // add error handler
    app.use(notFoundErrorHandler);
    app.use(domainErrHandler);

    app.listen(PORT, () => {
      logger.info('Express server listening on port ' + PORT);
    });
  }

  private initDB = async (dbType: DBType) => {
    switch (dbType) {
      case SEQUELIZE: {
        // initialize sequelize mysql
        //sequelize          .
        // await mysql.sequelize.sync();
        // mysql.sequelize.
        await mysql.sequelize.sync();
      }
    }
  };

  private getCorsOption() {
    return {
      // TODO update to https
      origin: [
        'https://sdf.splashpay.net',
        'http://7go.io.s3-website.ap-northeast-2.amazonaws.com',
        'https://develop.splashpay.net',
        'http://localhost:3000',
        'http://localhost:1234'
      ],
      optionSuccessStatus: 200
    };
  }

  private setSendgrid() {
    const sendgirdAPIKey = process.env.SENDGRID_API_KEY;
    if (sendgirdAPIKey === undefined) {
      throw new Error(
        'Does not find the sendgrid api key. please check your system environment variables.'
      );
    }
    mail.setApiKey(sendgirdAPIKey);
  }

  private generateSwaggerDefinitions() {
    // generate swagger models
    swaggerGenerator.generate(sequelizeModels, {
      type: 'sequelize',
      path: './src/server/infra/swagger/models.js'
    });

    let files = fs.readdirSync('./src/server/common/validateSchemas');
    let schemas = [];
    for (let file of files) {
      let fileObj = require('./common/validateSchemas/' + file);

      for (let key in fileObj) {
        if (typeof fileObj[key] === 'object') {
          let model = fileObj[key];
          model = model.tags(file.split('.')[0]);

          schemas.push(model);
        }
      }
    }
    swaggerGenerator.generate(schemas, {
      type: 'joi',
      path: './src/server/infra/swagger/schemas.js'
    });
  }
}
