import {
  Table,
  Model,
  Column,
  AutoIncrement,
  PrimaryKey,
  DataType,
  AllowNull,
  ForeignKey,
  BelongsTo,
  DefaultScope
} from 'sequelize-typescript';
import { Currency } from '@common/models/Currency';
import { Asset } from '@domain/asset/asset.model';

//   asset   .
//  asset   sales  ,  sales    softcap, hardcap  .
// ,  asset  cap         .

export enum SalesStatus {
  ACTIVE = 'ACTIVE',
  INACTIVE = 'INACTIVE'
}

@DefaultScope({ include: [{ model: () => Asset }] })
@Table({ modelName: 'sales' })
export class Sales extends Model<Sales> {
  @AutoIncrement
  @PrimaryKey
  @Column({ type: DataType.INTEGER })
  id: number;

  @AllowNull(false)
  @ForeignKey(() => Asset)
  @Column({ type: DataType.INTEGER })
  assetId: number;

  @Column({ type: DataType.ENUM({ values: Object.keys(SalesStatus) }) })
  status: SalesStatus;

  @AllowNull(false)
  @Column({ type: DataType.STRING })
  name: String;

  @AllowNull(false)
  @Column({ type: DataType.INTEGER, comment: ' ' })
  fiatPrice: number;

  @AllowNull(false)
  @Column({ type: DataType.ENUM({ values: Object.keys(Currency) }) })
  fiatCurrency: Currency;

  @AllowNull(false)
  @Column({
    type: DataType.FLOAT,
    comment: 'The amount of interest which should be distributed later 0~1'
  })
  interestAmount: number;

  @Column({ type: DataType.INTEGER })
  softCap: number;

  @Column({ type: DataType.INTEGER })
  hardCap: number;

  @Column({ type: DataType.INTEGER })
  individualSoftCap: number;

  @Column({ type: DataType.INTEGER })
  individualHardCap: number;

  @Column({
    type: DataType.INTEGER,
    comment: 'The minimum number of investors'
  })
  minInvestor: number;

  @Column({
    type: DataType.INTEGER,
    comment: 'The maximum number of investors'
  })
  maxInvestor: number;

  @Column({
    type: DataType.DATE,
    comment: 'The start date of this sales'
  })
  startAt: Date;

  @Column({
    type: DataType.DATE,
    comment: 'The end date of this sales'
  })
  endAt: Date;

  @BelongsTo(() => Asset)
  asset: Asset;

  @Column({ type: DataType.DATE })
  createdAt?: Date;

  @Column({ type: DataType.DATE })
  updatedAt?: Date;
}
