import {
  Table,
  DataType,
  Column,
  Model,
  PrimaryKey,
  AutoIncrement,
  CreatedAt,
  UpdatedAt,
  ForeignKey
} from 'sequelize-typescript';
import { Asset } from '@domain/asset/asset.model';
import { Order } from '@domain/order/order.model';
import { User } from '@domain/user/user.model';
import { Pocket } from '@domain/pocket/pocket.model';

export enum TransactionType {
  PAYMENT = 'PAYMENT',
  INTEREST = 'INTEREST',
  BONUS = 'BONUS',
  WITHDRAW = 'WITHDRAW'
}

export enum TransactionStatus {
  PENDING = 'PENDING',
  COMPLETE = 'COMPLETE',
  FAIL = 'FAIL'
}

@Table({ modelName: 'transaction' })
export class Transaction extends Model<Transaction> {
  @PrimaryKey
  @AutoIncrement
  @Column({ type: DataType.INTEGER })
  id: Number;

  @ForeignKey(() => Order)
  @Column({ type: DataType.INTEGER })
  orderId?: Number;

  @ForeignKey(() => Asset)
  @Column({ type: DataType.INTEGER })
  assetId: Number;

  @Column({ type: DataType.ENUM({ values: Object.keys(TransactionType) }) })
  type?: TransactionType;

  @Column({ type: DataType.ENUM({ values: Object.keys(TransactionStatus) }) })
  status?: TransactionStatus;

  @ForeignKey(() => User)
  @Column({ type: DataType.INTEGER })
  fromUserId: number;

  @ForeignKey(() => User)
  @Column({ type: DataType.INTEGER })
  toUserId: number;

  @ForeignKey(() => Pocket)
  @Column({ type: DataType.INTEGER })
  fromPocketId: number;

  @ForeignKey(() => Pocket)
  @Column({ type: DataType.INTEGER })
  toPocketId: number;

  @Column({ type: DataType.INTEGER })
  amount: Number;

  @Column({ type: DataType.STRING })
  currency: String;

  @CreatedAt
  createdAt?: Date;

  @UpdatedAt
  updatedAt?: Date;
}
