import {
  Model,
  Table,
  PrimaryKey,
  AutoIncrement,
  Column,
  DataType,
  CreatedAt,
  UpdatedAt,
  ForeignKey
} from 'sequelize-typescript';
import { User } from '@domain/user/user.model';

export class SplashAccount {
  constructor(phone: string) {
    this.phone = phone;
  }
  phone: string;
}

export enum PlatformType {
  SPLASH = 'SPLASH'
}

@Table({ modelName: 'connected-account' })
export class ConnectedAccount extends Model<ConnectedAccount> {
  @PrimaryKey
  @AutoIncrement
  @Column({ type: DataType.INTEGER })
  id: number;

  @ForeignKey(() => User)
  @Column({ type: DataType.INTEGER })
  userId: number;

  @Column({ type: DataType.ENUM({ values: Object.keys(PlatformType) }) })
  platformType: PlatformType;

  @Column({ type: DataType.JSON })
  value: SplashAccount;

  @CreatedAt
  createdAt: Date;

  @UpdatedAt
  updatedAt: Date;
}
