
  import { Route } from 'server/common/models/Route';
  import * as express from 'express';
  import { httpSuccessResponse } from '@utils/httpSender';
  import { CouponRepository } from '@domain/coupon/coupon.repository';
  import { FilterSchema } from '@common/validateSchemas/FilterSchema';
  import Joi from 'joi';
  import { Coupon } from '@domain/coupon/coupon.model';

  export class CouponRoute implements Route {
    private router: express.Router;
    constructor(private couponRepository: CouponRepository) {
      this.couponRepository = couponRepository;
      this.router = express.Router();
    }
    handle() {
      /**
       * @swagger
       * /coupons:
       *   post:
       *     description: Coupon API
       *     tags:
       *       - Coupons
       *     produces:
       *       - application/json
       *     parameters:
       *       - in: body
       *         name: body
       *         schema:
       *           $ref: '#/definitions/Coupon'
       *     responses:
       *       200:
       *         description: Success
       *         schema:
       *           $ref: '#/definitions/Coupon'
       */
      this.router.post('', (...args) => this.create(...args));
      /**
       * @swagger
       * /coupons:
       *   get:
       *     description: Coupon API
       *     tags:
       *       - Coupons
       *     produces:
       *       - application/json
       *     parameters:
       *       - in: query
       *         name: where
       *         type: object
       *         description: Where clause explains about the constraints used in find data
       *       - in: query
       *         name: limit
       *         type: integer
       *         description: How many data do you want?
       *       - in: query
       *         name: offset
       *         type: integer
       *         description: Where to start find data
       *     responses:
       *       200:
       *         description: Success
       *         schema:
       *           type: object
       *           properties:
       *             payload:
       *               type: array
       *               items:
       *                 $ref: '#/definitions/Coupon'
       */
      this.router.get('', (...args) => this.findAll(...args));
      /**
       * @swagger
       * /coupons/{id}:
       *   get:
       *     description: Coupon API
       *     tags:
       *       - Coupons
       *     produces:
       *       - application/json
       *     parameters:
       *       - in: path
       *         name: id
       *         type: number
       *         description: Single coupon
       *     responses:
       *       200:
       *         description: Success
       *         schema:
       *           $ref: '#/definitions/Coupon'
       */
      this.router.get('/:id', (...args) => this.findById(...args));
      /**
       * @swagger
       * /coupons:
       *   put:
       *     description: Update coupon
       *     tags:
       *       - Coupons
       *     produces:
       *       - application/json
       *     parameters:
       *       - in: body
       *         name: body
       *         schema:
       *           properties:
       *             data:
       *               $ref: '#/definitions/Coupon'
       *             option:
       *               type: object
       *               schema:
       *                 type: object
       *                 properties:
       *                   where:
       *                     type: string
       *     responses:
       *       200:
       *         description: Success
       *         schema:
       *           $ref: '#/definitions/Coupon'
       */
      this.router.put('', (...args) => this.update(...args));
  
      /**
       * @swagger
       * /coupons:
       *   delete:
       *     description: Delete coupon
       *     tags:
       *       - Coupons
       *     produces:
       *       - application/json
       *     parameters:
       *       - in: body
       *         name: body
       *         schema:
       *           $ref: '#/definitions/Coupon'
       *     responses:
       *       200:
       *         description: Success
       *         schema:
       *           properties:
       *             count:
       *               type: integer
       */
      this.router.delete('', (...args) => this.remove(...args));
      return this.router;
    }
  
    private async create(
      req: express.Request,
      res: express.Response,
      next: express.NextFunction
    ) {
      try {        
        let coupon = new Coupon(req.body)
        let created = await this.couponRepository.save(coupon);
        return res.send(created);
      } catch (err) {
        return next(err);
      }
    }
  
    // @authGuard
    private async findAll(
      req: express.Request,
      res: express.Response,
      next: express.NextFunction
    ) {
      try {
        let { value, error } = Joi.validate(req.query, FilterSchema, {
          convert: true
        });
  
        if (error) return next(error);
        
        let payload = await this.couponRepository.findAll(value);
        return httpSuccessResponse(res, {payload});
      } catch (err) {
        return next(err);
      }
    }
  
    private async findById(
      req: express.Request,
      res: express.Response,
      next: express.NextFunction
    ) {
      try {
        let id = req.params.id;
        let coupon = await this.couponRepository.findById(id);
  
        return res.send(coupon);
      } catch (err) {
        return next(err);
      }
    }
  
    private async update(
      req: express.Request,
      res: express.Response,
      next: express.NextFunction
    ) {
      try {
        let { data, option } = req.body;
        let [count, payload] = await this.couponRepository.update(data, option);
  
        return httpSuccessResponse(res, {payload, count});
      } catch (err) {
        return next(err);
      }
    }
  
    private async remove(
      req: express.Request,
      res: express.Response,
      next: express.NextFunction
    ) {
      try {
        let option = req.body;
        let count = await this.couponRepository.delete(option);
  
        return httpSuccessResponse(res, { count })
      } catch (err) {
        return next(err);
      }
    }
  }
  