import { Sequelize } from 'sequelize-typescript';
import path from 'path';
import { User } from '@domain/user/user.model';
import { Transaction } from '@domain/transaction/transaction.model';
import { Asset } from '@domain/asset/asset.model';
import { AssetImage } from '@domain/asset-image/asset-image.model';
import { AssetDesc } from '@domain/asset-desc/asset-desc.model';
import { AppliedCoupon } from '@domain/applied-coupon/applied-coupon.model';
import { Category } from '@domain/category/category.model';
import { Coupon } from '@domain/coupon/coupon.model';
import { EthereumConfig } from '@domain/ethereum-config/ethereum-config.model';
import { ExchangeRate } from '@domain/exchange-rate/exchange-rate.model';
import { ExternalWallet } from '@domain/external-wallet/external-wallet.model';
import { Order } from '@domain/order/order.model';
import { Reward } from '@domain/reward/reward.model';
import { Sales } from '@domain/sales/sales.model';
import { SalesProposal } from '@domain/sales-proposal/sales-proposal.model';
import { Token } from '@domain/token/token.model';
import { MonthlyAssetInfo } from '@domain/monthly-asset-info/monthly-asset-info.model';
import { Wallet } from '@domain/wallet/wallet.model';
import { Pocket } from '@domain/pocket/pocket.model';
import { ConnectedAccount } from '@domain/connected-account/connected-account.model';
import { ApiUser } from '@domain/api-user/api-user.model';

require('dotenv').config();

const sequelize = new Sequelize({
  database: process.env.MYSQL_DB_NAME,
  username: process.env.MYSQL_USER,
  password: process.env.MYSQL_PASSWORD,
  port: process.env.MYSQL_PORT,
  dialect: process.env.DB_TYPE,
  url: process.env.MYSQL_URL,
  pool: {
    max: 5,
    min: 0,
    acquire: 30000,
    idle: 10000
  }
});

export const sequelizeModels = [
  AppliedCoupon,
  Category,
  Coupon,
  EthereumConfig,
  ExchangeRate,
  ExternalWallet,
  Order,
  Reward,
  Sales,
  SalesProposal,
  Transaction,
  User,
  Asset,
  AssetImage,
  AssetDesc,
  MonthlyAssetInfo,
  Wallet,
  Pocket,
  ConnectedAccount,
  ApiUser,
  Token
];

sequelize.addModels(sequelizeModels);

export default {
  sequelize
};
