import Joi from 'joi';

export const CreateSalesSchema = Joi.object().keys({
  assetId: Joi.number().required(),
  name: Joi.string().required(),
  fiatPrice: Joi.number().required(),
  fiatCurrency: Joi.string().required(),
  hardCap: Joi.number()
    .positive()
    .required(),
  individualSoftCap: Joi.number().required(),
  interestAmount: Joi.number().required(),
  startAt: Joi.date().required(),
  endAt: Joi.date().required()
});
