import { AssetDesc } from '@domain/asset-desc/asset-desc.model';
import { DestroyOptions, UpdateOptions } from 'sequelize';
import { Filter } from '@common/models/QueryOption';
import { ICountOptions } from 'sequelize-typescript';

export class AssetDescRepositoryImpl {
  constructor() {}

  async save(assetDesc: AssetDesc) {
    const _ = AssetDesc.build(assetDesc);
    return await _.save();
  }

  async findAll(filter: Filter) {
    return await AssetDesc.findAll(filter);
  }

  async findById(id: number) {
    return await AssetDesc.findByPrimary(id);
  }

  async getCount(filter: ICountOptions<AssetDesc>) {
    return await AssetDesc.count(filter);
  }

  async update(
    assetDesc: Partial<AssetDesc>,
    option: UpdateOptions
  ): Promise<[number, AssetDesc[]]> {
    return await AssetDesc.update(assetDesc, option);
  }

  async delete(option: DestroyOptions): Promise<number> {
    return await AssetDesc.destroy(option);
  }
}
