import { Order } from '../order/order.model';
import {
  Table,
  Model,
  Column,
  DataType,
  PrimaryKey,
  AutoIncrement,
  ForeignKey,
  CreatedAt,
  UpdatedAt,
  AllowNull,
  BelongsTo,
  DefaultScope
} from 'sequelize-typescript';
import { Coupon } from '@domain/coupon/coupon.model';

@DefaultScope({ include: [() => Coupon] })
@Table({
  modelName: 'applied-coupon',
  underscored: true,
  comment: 'The list of coupons which is applied to specific order'
})
export class AppliedCoupon extends Model<AppliedCoupon> {
  @PrimaryKey
  @AutoIncrement
  @Column({
    type: DataType.INTEGER
  })
  id: number;

  @AllowNull(false)
  @ForeignKey(() => Order)
  @Column({ type: DataType.INTEGER })
  orderId: number;

  @AllowNull(false)
  @ForeignKey(() => Coupon)
  @Column({
    type: DataType.INTEGER,
    comment: 'The Id of the coupon which is applied to specific order'
  })
  couponId: number;

  @CreatedAt
  createdAt: Date;

  @UpdatedAt
  updatedAt: Date;

  @BelongsTo(() => Coupon)
  coupon: Coupon;
}
