import {
  Table,
  Model,
  Column,
  DataType,
  PrimaryKey,
  AutoIncrement,
  ForeignKey,
  AllowNull,
  CreatedAt,
  UpdatedAt
} from 'sequelize-typescript';
import { Asset } from '@domain/asset/asset.model';
import { User } from '@domain/user/user.model';

@Table({ modelName: 'coupon' })
export class Coupon extends Model<Coupon> {
  @PrimaryKey
  @AutoIncrement
  @Column({ type: DataType.INTEGER })
  id: number;

  @AllowNull(false)
  @ForeignKey(() => User)
  @Column({
    type: DataType.INTEGER,
    comment: 'The Id of the user who published coupon'
  })
  publisher: number;

  @AllowNull(false)
  @ForeignKey(() => User)
  @Column({ type: DataType.INTEGER })
  receiver: number;

  @Column({
    type: DataType.STRING
  })
  title?: string;

  @AllowNull(false)
  @ForeignKey(() => Asset)
  @Column({ type: DataType.INTEGER })
  assetId: number;

  @AllowNull(false)
  @Column({
    type: DataType.INTEGER,
    comment:
      'The amount of ratio to be applied to receiver when transaction happens'
  })
  amount: number;

  @CreatedAt
  creatdAt: Date;

  @UpdatedAt
  updatedAt: Date;
}
