import { Coupon } from '@domain/coupon/coupon.model';
import { DestroyOptions, UpdateOptions } from 'sequelize';
import { Filter } from '@common/models/QueryOption';
import { ICountOptions } from 'sequelize-typescript';
import { removeDotInJson } from '@frontalnh/json-dot-parser';

export class CouponRepositoryImpl {
  constructor() {}

  async save(coupon: Coupon) {
    return await coupon.save();
  }

  async findAll(filter: Filter) {
    if (!filter.raw) return await Coupon.findAll(filter);

    let datas = await Coupon.findAll(filter);
    for (let data of datas) {
      Object.assign(data, removeDotInJson(data));
    }

    return datas;
  }

  async findById(id: number) {
    return await Coupon.findByPrimary(id);
  }

  async getCount(filter: ICountOptions<Coupon>) {
    return await Coupon.count(filter);
  }

  async update(
    coupon: Partial<Coupon>,
    option: UpdateOptions
  ): Promise<[number, Coupon[]]> {
    return await Coupon.update(coupon, option);
  }

  async delete(option: DestroyOptions): Promise<number> {
    return await Coupon.destroy(option);
  }
}
