import { ConnectedAccountRepository } from '@domain/connected-account/connected-account.repository';
import {
  ConnectedAccount,
  PlatformType,
  SplashAccount
} from '@domain/connected-account/connected-account.model';

export class ConnectedAccountService {
  constructor(private connectedAccountRepository: ConnectedAccountRepository) {
    this.connectedAccountRepository = connectedAccountRepository;
  }

  async createSplashAccount(userId, phone): Promise<ConnectedAccount> {
    let connectedAccount = new ConnectedAccount();
    connectedAccount.userId = userId;
    connectedAccount.platformType = PlatformType.SPLASH;
    connectedAccount.value = new SplashAccount(phone);

    return await this.connectedAccountRepository.save(connectedAccount);
  }
}
