
  import { ExternalWallet } from '@domain/external-wallet/external-wallet.model';
  import { DestroyOptions, UpdateOptions } from 'sequelize';
  import { Filter } from '@common/models/QueryOption';
  import { ICountOptions } from 'sequelize-typescript';
  import { removeDotInJson } from '@frontalnh/json-dot-parser';

  export class ExternalWalletRepositoryImpl {
    constructor() {}
  
    async save(externalWallet: ExternalWallet) {
      return await externalWallet.save();
    }
  
    async findAll(filter: Filter) {
      if (!filter.raw) return await ExternalWallet.findAll(filter);

      let datas = await ExternalWallet.findAll(filter);
      for (let data of datas) {
        Object.assign(data, removeDotInJson(data));
      }
  
      return datas;
    }
  
    async findById(id: number) {
      return await ExternalWallet.findByPrimary(id);
    }

    async getCount(filter: ICountOptions<ExternalWallet>) {
      return await ExternalWallet.count(filter);
    }
  
    async update(
      externalWallet: Partial<ExternalWallet>,
      option: UpdateOptions
    ): Promise<[number, ExternalWallet[]]> {
      return await ExternalWallet.update(externalWallet, option);
    }
  
    async delete(option: DestroyOptions): Promise<number> {
      return await ExternalWallet.destroy(option);
    }
  }
  