import {
  DataType,
  Model,
  Table,
  Column,
  PrimaryKey,
  AutoIncrement,
  CreatedAt,
  UpdatedAt
} from 'sequelize-typescript';

@Table({ modelName: 'exchange-rate' })
export class ExchangeRate extends Model<ExchangeRate> {
  @PrimaryKey
  @AutoIncrement
  @Column({ type: DataType.INTEGER })
  id: Number;

  @Column({ type: DataType.INTEGER })
  assetId: Number;

  @Column({ type: DataType.INTEGER })
  tokenId: Number;

  @Column({ type: DataType.INTEGER })
  ratio: Number;

  @CreatedAt
  createdAt?: Date;

  @UpdatedAt
  updatedAt?: Date;
}
