import { MonthlyAssetInfo } from '@domain/monthly-asset-info/monthly-asset-info.model';
import { UpdateOption, Filter } from '@common/models/QueryOption';
import { DestroyOptions } from 'sequelize';
import sequelize = require('sequelize');
import { MonthlyAssetInfoRepository } from '@domain/monthly-asset-info/monthly-asset-info.repository';
import { Asset } from '@domain/asset/asset.model';

export class MonthlyAssetInfoService {
  constructor(
    private readonly monthlyAssetInfoRepository: MonthlyAssetInfoRepository
  ) {
    this.monthlyAssetInfoRepository = monthlyAssetInfoRepository;
  }
  public async getThisMonthRevenue(asset: Asset): Promise<number> {
    let thisYear = new Date().getFullYear();
    let thisMonth = new Date().getMonth() + 1;
    let startDate;
    if (thisMonth === 1) {
      startDate = new Date(
        thisYear + '-' + (thisMonth - 1) + '-' + asset.allocationDate
      );
    } else {
      startDate = new Date(
        thisYear - 1 + '-' + 12 + '-' + asset.allocationDate
      );
    }

    let endDate = new Date(
      thisYear + '-' + thisMonth + '-' + asset.allocationDate
    );

    console.log('#####', asset);
    let _ = await this.monthlyAssetInfoRepository.findAll({
      where: {
        createdAt: {
          [sequelize.Op.gt]: startDate,
          [sequelize.Op.lt]: endDate
        }
      }
    });

    if (_.length === 0) return 0;

    return _[0].revenue;
  }
}
