import { Route } from 'server/common/models/Route';
import * as express from 'express';
import { httpSuccessResponse } from '@utils/httpSender';
import { AssetRepository } from '@domain/asset/asset.repository';
import { AssetApi } from '@api/asset.api';

export class AssetRoute implements Route {
  private router: express.Router;
  constructor(
    private assetRepository: AssetRepository,
    private assetApi: AssetApi
  ) {
    this.assetRepository = assetRepository;
    this.assetApi = assetApi;
    this.router = express.Router();
  }

  handle() {
    /**
     * @swagger
     * /assets:
     *   post:
     *     description: Asset API
     *     tags:
     *       - Assets
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: body
     *         name: body
     *         schema:
     *           $ref: '#/definitions/CreateAssetSchema'
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           $ref: '#/definitions/Asset'
     */
    this.router.post('', (...args) => this.create(...args));
    /**
     * @swagger
     * /assets:
     *   get:
     *     description: Asset API
     *     tags:
     *       - Assets
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: query
     *         name: where
     *         type: object
     *         description: Where clause explains about the constraints used in find data
     *       - in: query
     *         name: limit
     *         type: integer
     *         description: How many data do you want?
     *       - in: query
     *         name: offset
     *         type: integer
     *         description: Where to start find data
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           type: object
     *           properties:
     *             payload:
     *               type: array
     *               items:
     *                 $ref: '#/definitions/Asset'
     */
    this.router.get('', (...args) => this.findAll(...args));
    /**
     * @swagger
     * /assets/{id}:
     *   get:
     *     description: Asset API
     *     tags:
     *       - Assets
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: path
     *         name: id
     *         type: number
     *         description: Single asset
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           $ref: '#/definitions/Asset'
     */
    this.router.get('/:id', (...args) => this.findById(...args));

    /**
     * @swagger
     * /assets/with-statistic-info:
     *   get:
     *     description: sdfsd
     *     tags:
     *       - Asset
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: query
     *         name: sdf
     *         type: sdf
     *         description: sdf
     *     responses:
     *       200:
     *         description: sdf
     *         schema:
     *           $ref: '#/definitions/Asset'
     */
    this.router.get('/with-statistic-info', (...args) =>
      this.findAllWithStatisticInfo(...args)
    );
    /**
     * @swagger
     * /assets:
     *   put:
     *     description: Update asset
     *     tags:
     *       - Assets
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: body
     *         name: body
     *         schema:
     *           properties:
     *             data:
     *               $ref: '#/definitions/Asset'
     *             option:
     *               type: object
     *               schema:
     *                 type: object
     *                 properties:
     *                   where:
     *                     type: string
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           $ref: '#/definitions/Asset'
     */
    this.router.put('', (...args) => this.update(...args));

    /**
     * @swagger
     * /assets:
     *   delete:
     *     description: Delete asset
     *     tags:
     *       - Assets
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: body
     *         name: body
     *         schema:
     *           $ref: '#/definitions/Asset'
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           properties:
     *             count:
     *               type: integer
     */
    this.router.delete('', (...args) => this.remove(...args));
    return this.router;
  }

  private async create(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let asset = req.body;

      let created = await this.assetRepository.save(asset);
      return res.send(created);
    } catch (err) {
      return next(err);
    }
  }

  // @authGuard
  private async findAll(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      if (req.query.where && req.query.where.userId) {
        let assets = await this.assetApi.findAssetWithStatisticInfo(req.query);
        return httpSuccessResponse(res, assets);
      } else {
        let payload = await this.assetRepository.findAll(req.query);
        return httpSuccessResponse(res, { payload });
      }
    } catch (err) {
      return next(err);
    }
  }

  // @authGuard
  private async findAllWithStatisticInfo(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let assets = await this.assetApi.findAssetWithStatisticInfo(req.query);
      return httpSuccessResponse(res, { payload: assets });
    } catch (err) {
      return next(err);
    }
  }

  private async findById(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let id = req.params.id;
      let asset = await this.assetRepository.findById(id);

      return res.send(asset);
    } catch (err) {
      return next(err);
    }
  }

  private async update(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let { data, option } = req.body;
      let [count, payload] = await this.assetRepository.update(data, option);

      return httpSuccessResponse(res, { payload, count });
    } catch (err) {
      return next(err);
    }
  }

  private async remove(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let option = req.body;
      let count = await this.assetRepository.delete(option);

      res.send(count.toString());
    } catch (err) {
      return next(err);
    }
  }
}
