import { TransactionRepository } from '@domain/transaction/transaction.repository';
import { Filter } from '@common/models/QueryOption';
import { AssetRepository } from '@domain/asset/asset.repository';
import assert = require('assert');
import { MonthlyAssetInfoService } from '@domain/monthly-asset-info/monthly-asset-info.service';

export class AssetApi {
  constructor(
    private readonly assetRepository: AssetRepository,
    private readonly transactionRepository: TransactionRepository,
    private readonly monthlyAssetInfoService: MonthlyAssetInfoService
  ) {}

  async findAssetWithStatisticInfo(filter: Filter): Promise<any> {
    let userId = parseInt(filter.where.userId);

    assert.equal(typeof filter.where.userId, 'string');
    assert.equal(typeof userId, 'number');

    let assets = await this.assetRepository.findAll({
      where: { userId },
      raw: true
    });

    for (let asset of assets) {
      let {
        totalInvestAmount
      } = await this.transactionRepository.getTotalAmount(userId, asset.id);
      asset.totalInvestAmount = totalInvestAmount;
      asset.totalThisMonthRevenue = await this.monthlyAssetInfoService.getThisMonthRevenue(
        asset
      );
    }

    return assets;
  }
}
