import {
  Model,
  Column,
  DataType,
  PrimaryKey,
  AllowNull,
  Table
} from 'sequelize-typescript';

@Table({ modelName: 'api-user', underscored: true })
export class ApiUser extends Model<ApiUser> {
  @PrimaryKey
  @Column({ type: DataType.STRING, comment: 'API    id .' })
  id: string;

  @AllowNull(false)
  @Column({ type: DataType.STRING })
  name: string;

  @AllowNull(false)
  @Column({ type: DataType.STRING })
  password: string;
}
