
  import { Sales } from '@domain/sales/sales.model';
  import { DestroyOptions, UpdateOptions } from 'sequelize';
  import { Filter } from '@common/models/QueryOption';
  import { ICountOptions } from 'sequelize-typescript';

  export class SalesRepositoryImpl {
    constructor() {}
  
    async save(sales: Sales) {
      return await sales.save();
    }
  
    async findAll(filter: Filter) {
      return await Sales.findAll(filter);
    }
  
    async findById(id: number) {
      return await Sales.findByPrimary(id);
    }

    async getCount(filter: ICountOptions<Sales>) {
      return await Sales.count(filter);
    }
  
    async update(
      sales: Partial<Sales>,
      option: UpdateOptions
    ): Promise<[number, Sales[]]> {
      return await Sales.update(sales, option);
    }
  
    async delete(option: DestroyOptions): Promise<number> {
      return await Sales.destroy(option);
    }
  }
  