import {
  Table,
  ForeignKey,
  Column,
  DataType,
  CreatedAt,
  UpdatedAt,
  PrimaryKey,
  AutoIncrement,
  BelongsTo,
  Model
} from 'sequelize-typescript';
import { Asset } from '@domain/asset/asset.model';

@Table({ modelName: 'monthly-asset-info', underscored: true })
export class MonthlyAssetInfo extends Model<MonthlyAssetInfo> {
  @PrimaryKey
  @AutoIncrement
  @Column({ type: DataType.INTEGER })
  id: number;

  @ForeignKey(() => Asset)
  @Column({ type: DataType.INTEGER })
  assetId: number;

  @Column({ type: DataType.INTEGER })
  salesAmount: number;

  @Column({ type: DataType.INTEGER })
  taxAmount: number;

  @Column({ type: DataType.INTEGER })
  operationFee: number;

  @Column({ type: DataType.INTEGER })
  revenue: number;

  @CreatedAt
  createdAt: Date;

  @UpdatedAt
  updatedAt: Date;

  @BelongsTo(() => Asset)
  asset: Asset;
}
