import { Route } from 'server/common/models/Route';
import * as express from 'express';
import { httpSuccessResponse } from '@utils/httpSender';
import { AppliedCouponRepository } from '@domain/applied-coupon/applied-coupon.repository';
import { FilterSchema } from '@common/validateSchemas/FilterSchema';
import Joi from 'joi';
import { AppliedCoupon } from '@domain/applied-coupon/applied-coupon.model';

export class AppliedCouponRoute implements Route {
  private router: express.Router;
  constructor(private appliedCouponRepository: AppliedCouponRepository) {
    this.appliedCouponRepository = appliedCouponRepository;
    this.router = express.Router();
  }
  handle() {
    /**
     * @swagger
     * /applied-coupons:
     *   post:
     *     description: AppliedCoupon API
     *     tags:
     *       - AppliedCoupons
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: body
     *         name: body
     *         schema:
     *           $ref: '#/definitions/CreateAppliedCouponSchema'
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           $ref: '#/definitions/AppliedCoupon'
     */
    this.router.post('', (...args) => this.create(...args));
    /**
     * @swagger
     * /applied-coupons:
     *   get:
     *     description: AppliedCoupon API
     *     tags:
     *       - AppliedCoupons
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: query
     *         name: where
     *         type: object
     *         description: Where clause explains about the constraints used in find data
     *       - in: query
     *         name: limit
     *         type: integer
     *         description: How many data do you want?
     *       - in: query
     *         name: offset
     *         type: integer
     *         description: Where to start find data
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           type: object
     *           properties:
     *             payload:
     *               type: array
     *               items:
     *                 $ref: '#/definitions/AppliedCoupon'
     */
    this.router.get('', (...args) => this.findAll(...args));
    /**
     * @swagger
     * /applied-coupons/{id}:
     *   get:
     *     description: AppliedCoupon API
     *     tags:
     *       - AppliedCoupons
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: path
     *         name: id
     *         type: number
     *         description: Single appliedCoupon
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           $ref: '#/definitions/AppliedCoupon'
     */
    this.router.get('/:id', (...args) => this.findById(...args));
    /**
     * @swagger
     * /applied-coupons:
     *   put:
     *     description: Update appliedCoupon
     *     tags:
     *       - AppliedCoupons
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: body
     *         name: body
     *         schema:
     *           properties:
     *             data:
     *               $ref: '#/definitions/AppliedCoupon'
     *             option:
     *               type: object
     *               schema:
     *                 type: object
     *                 properties:
     *                   where:
     *                     type: string
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           $ref: '#/definitions/AppliedCoupon'
     */
    this.router.put('', (...args) => this.update(...args));

    /**
     * @swagger
     * /applied-coupons:
     *   delete:
     *     description: Delete appliedCoupon
     *     tags:
     *       - AppliedCoupons
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: body
     *         name: body
     *         schema:
     *           $ref: '#/definitions/AppliedCoupon'
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           properties:
     *             count:
     *               type: integer
     */
    this.router.delete('', (...args) => this.remove(...args));
    return this.router;
  }

  private async create(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let appliedCoupon = new AppliedCoupon(req.body);
      let created = await this.appliedCouponRepository.save(appliedCoupon);
      return res.send(created);
    } catch (err) {
      return next(err);
    }
  }

  // @authGuard
  private async findAll(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let { value, error } = Joi.validate(req.query, FilterSchema, {
        convert: true
      });

      if (error) return next(error);

      let payload = await this.appliedCouponRepository.findAll(value);
      return httpSuccessResponse(res, { payload });
    } catch (err) {
      return next(err);
    }
  }

  private async findById(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let id = req.params.id;
      let appliedCoupon = await this.appliedCouponRepository.findById(id);

      return res.send(appliedCoupon);
    } catch (err) {
      return next(err);
    }
  }

  private async update(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let { data, option } = req.body;
      let [count, payload] = await this.appliedCouponRepository.update(
        data,
        option
      );

      return httpSuccessResponse(res, { payload, count });
    } catch (err) {
      return next(err);
    }
  }

  private async remove(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let option = req.body;
      let count = await this.appliedCouponRepository.delete(option);

      return httpSuccessResponse(res, { count });
    } catch (err) {
      return next(err);
    }
  }
}
