import {
  DataType,
  Column,
  Table,
  CreatedAt,
  UpdatedAt,
  PrimaryKey,
  AutoIncrement,
  Model
} from 'sequelize-typescript';

@Table({ modelName: 'ethereum-config' })
export class EthereumConfig extends Model<EthereumConfig> {
  @PrimaryKey
  @AutoIncrement
  @Column({
    type: DataType.INTEGER
  })
  id: Number;

  @Column({
    type: DataType.STRING
  })
  key: String;

  @Column({
    type: DataType.STRING
  })
  value: string;

  @CreatedAt
  createdAt?: Date;

  @UpdatedAt
  updatedAt?: Date;
}
