
  import { EthereumConfig } from '@domain/ethereum-config/ethereum-config.model';
  import { DestroyOptions, UpdateOptions } from 'sequelize';
  import { Filter } from '@common/models/QueryOption';
  import { ICountOptions } from 'sequelize-typescript';
  import { removeDotInJson } from '@frontalnh/json-dot-parser';

  export class EthereumConfigRepositoryImpl {
    constructor() {}
  
    async save(ethereumConfig: EthereumConfig) {
      return await ethereumConfig.save();
    }
  
    async findAll(filter: Filter) {
      if (!filter.raw) return await EthereumConfig.findAll(filter);

      let datas = await EthereumConfig.findAll(filter);
      for (let data of datas) {
        Object.assign(data, removeDotInJson(data));
      }
  
      return datas;
    }
  
    async findById(id: number) {
      return await EthereumConfig.findByPrimary(id);
    }

    async getCount(filter: ICountOptions<EthereumConfig>) {
      return await EthereumConfig.count(filter);
    }
  
    async update(
      ethereumConfig: Partial<EthereumConfig>,
      option: UpdateOptions
    ): Promise<[number, EthereumConfig[]]> {
      return await EthereumConfig.update(ethereumConfig, option);
    }
  
    async delete(option: DestroyOptions): Promise<number> {
      return await EthereumConfig.destroy(option);
    }
  }
  