import Joi from 'joi';
import { AssetType, Country } from '@domain/asset/asset.model';

export const CreateAssetSchema = Joi.object().keys({
  userId: Joi.number().required(),
  name: Joi.string().required(),
  type: Joi.string()
    .required()
    .valid(Object.keys(AssetType)),
  coutry: Joi.string()
    .valid(Object.keys(Country))
    .required(),
  address: Joi.string()
    .required()
    .min(10),
  minInterest: Joi.number().positive(),
  maxInterest: Joi.number()
    .max(100)
    .required(),
  tokenId: Joi.number()
    .positive()
    .required()
});
