import { WalletRepository } from '@domain/wallet/wallet.repository';
import { CustomError } from '@common/models/CustomError';
import { HttpErrCode } from '@common/constants/HttpErrCode';

export class WalletService {
  constructor(private walletRepository: WalletRepository) {
    this.walletRepository = walletRepository;
  }

  async popWallet() {
    let wallets = await this.walletRepository.findAll({
      where: { available: true }
    });
    if (wallets.length === 0) {
      throw new CustomError(
        HttpErrCode.REGISTER.NO_AVAILABLE_WALLET,
        '   .'
      );
    }

    let wallet = wallets[0];
    wallet.available = false;
    await this.walletRepository.save(wallet);

    return wallet;
  }
}
