import { Route } from 'server/common/models/Route';
import * as express from 'express';
import { httpSuccessResponse } from '@utils/httpSender';
import { ApiUserRepository } from '@domain/api-user/api-user.repository';
import { FilterSchema } from '@common/validateSchemas/FilterSchema';
import Joi from 'joi';
import { ApiUser } from '@domain/api-user/api-user.model';
import { GetApiTokenSchema } from '@common/validateSchemas/GetApiTokenSchema';
import { CreateApiUserSchema } from '@common/validateSchemas/CreateApiUserSchema';
import { CustomError } from '@common/models/CustomError';
import { HttpErrCode } from '@common/constants/HttpErrCode';
import { encrypt, encodeJwt } from '@utils/authenticator';
import * as bcrypt from 'bcryptjs';

export class ApiUserRoute implements Route {
  private router: express.Router;
  constructor(private apiUserRepository: ApiUserRepository) {
    this.apiUserRepository = apiUserRepository;
    this.router = express.Router();
  }
  handle() {
    /**
     * @swagger
     * /api-users:
     *   post:
     *     description: ApiUser API
     *     tags:
     *       - ApiUsers
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: body
     *         name: body
     *         schema:
     *           $ref: '#/definitions/CreateApiUserSchema'
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           $ref: '#/definitions/ApiUser'
     */
    this.router.post('', (...args) => this.create(...args));

    /**
     * @swagger
     * /api-users/get-api-token:
     *   post:
     *     description: API      sa-jwt .
     *     tags:
     *       - ApiUsers
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: body
     *         name: body
     *         type: object
     *         schema:
     *           $ref: '#/definitions/GetApiTokenSchema'
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           $ref: '#/definitions/ApiUser'
     */
    this.router.post('/get-api-token', (...args) => this.getApiToken(...args));
    /**
     * @swagger
     * /api-users:
     *   get:
     *     description: ApiUser API
     *     tags:
     *       - ApiUsers
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: query
     *         name: where
     *         type: object
     *         description: Where clause explains about the constraints used in find data
     *       - in: query
     *         name: limit
     *         type: integer
     *         description: How many data do you want?
     *       - in: query
     *         name: offset
     *         type: integer
     *         description: Where to start find data
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           type: object
     *           properties:
     *             payload:
     *               type: array
     *               items:
     *                 $ref: '#/definitions/ApiUser'
     */
    this.router.get('', (...args) => this.findAll(...args));
    /**
     * @swagger
     * /api-users/{id}:
     *   get:
     *     description: ApiUser API
     *     tags:
     *       - ApiUsers
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: path
     *         name: id
     *         type: number
     *         description: Single apiUser
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           $ref: '#/definitions/ApiUser'
     */
    this.router.get('/:id', (...args) => this.findById(...args));
    /**
     * @swagger
     * /api-users:
     *   put:
     *     description: Update apiUser
     *     tags:
     *       - ApiUsers
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: body
     *         name: body
     *         schema:
     *           properties:
     *             data:
     *               $ref: '#/definitions/ApiUser'
     *             option:
     *               type: object
     *               schema:
     *                 type: object
     *                 properties:
     *                   where:
     *                     type: string
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           $ref: '#/definitions/ApiUser'
     */
    this.router.put('', (...args) => this.update(...args));

    /**
     * @swagger
     * /api-users:
     *   delete:
     *     description: Delete apiUser
     *     tags:
     *       - ApiUsers
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: body
     *         name: body
     *         schema:
     *           $ref: '#/definitions/ApiUser'
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           properties:
     *             count:
     *               type: integer
     */
    this.router.delete('', (...args) => this.remove(...args));
    return this.router;
  }

  private async getApiToken(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    let { error } = Joi.validate(req.body, GetApiTokenSchema);
    if (error) return next(error);
    let { id, password } = req.body;

    let user = await this.apiUserRepository.findById(id);

    if (!user)
      return next(
        new CustomError(HttpErrCode.API.NO_USER, '  .')
      );

    let isValid = bcrypt.compareSync(password, user.password);
    if (!isValid)
      return next(
        new CustomError(
          HttpErrCode.API.INVALID_PASSWORD,
          '  .'
        )
      );

    let jwt = encodeJwt({ user: { id: user.id, name: user.name } }, 60 * 60);

    res.cookie('sa-jwt', jwt);
    return res.send(user);
  }

  private async create(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let { error } = Joi.validate(req.body, CreateApiUserSchema);
      if (error) return next(error);

      let { id, password, name } = req.body;

      let existUser = await this.apiUserRepository.findById(id);
      if (existUser)
        throw new CustomError(
          HttpErrCode.API.EXIST_ID,
          '  id .'
        );

      password = encrypt(password);
      let apiUser = new ApiUser({ id, password, name });
      let created = await this.apiUserRepository.save(apiUser);

      return res.send(created);
    } catch (err) {
      return next(err);
    }
  }

  // @authGuard
  private async findAll(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let { value, error } = Joi.validate(req.query, FilterSchema, {
        convert: true
      });

      if (error) return next(error);

      let payload = await this.apiUserRepository.findAll(value);
      return httpSuccessResponse(res, { payload });
    } catch (err) {
      return next(err);
    }
  }

  private async findById(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let id = req.params.id;
      let apiUser = await this.apiUserRepository.findById(id);

      return res.send(apiUser);
    } catch (err) {
      return next(err);
    }
  }

  private async update(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let { data, option } = req.body;
      let [count, payload] = await this.apiUserRepository.update(data, option);

      return httpSuccessResponse(res, { payload, count });
    } catch (err) {
      return next(err);
    }
  }

  private async remove(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let option = req.body;
      let count = await this.apiUserRepository.delete(option);

      return httpSuccessResponse(res, { count });
    } catch (err) {
      return next(err);
    }
  }
}
