import * as express from 'express';
import { OrderRoute } from '@interfaces/http/order.route';
import swaggerRoute from '@interfaces/http/swagger.route';
import { UserRoute } from '@interfaces/http/user.route';
import { Components } from 'server/App';
import { AppliedCouponRoute } from '@interfaces/http/applied-coupon.route';
import { AssetImageRoute } from '@interfaces/http/asset-image.route';
import { AssetDescRoute } from '@interfaces/http/asset-desc.route';
import { AssetRoute } from '@interfaces/http/asset.route';
import { CategoryRoute } from '@interfaces/http/category.route';
import { CouponRoute } from '@interfaces/http/coupon.route';
import { EthereumConfigRoute } from '@interfaces/http/ethereum-config.route';
import { ExchangeRateRoute } from '@interfaces/http/exchange-rate.route';
import { ExternalWalletRoute } from '@interfaces/http/external-wallet.route';
import { RewardRoute } from '@interfaces/http/reward.route';
import { SalesProposalRoute } from '@interfaces/http/sales-proposal.route';
import { SalesRoute } from '@interfaces/http/sales.route';
import { TokenRoute } from '@interfaces/http/token.route';
import { TransactionRoute } from '@interfaces/http/transaction.route';
import { WalletRoute } from '@interfaces/http/wallet.route';
import { MonthlyAssetInfoRoute } from '@interfaces/http/monthly-asset-info.route';
import { AuthRoute } from '@interfaces/http/auth.route';
import { MigrationRoute } from '@interfaces/http/migration.route';
import { ApiUserRoute } from '@interfaces/http/api-user.route';

export const router = (components: Components) => {
  let _router = express.Router();
  const appliedCouponRoute = new AppliedCouponRoute(
    components.appliedCouponRepository
  );
  const assetDescRoute = new AssetDescRoute(components.assetDescRepository);
  const assetImageRoute = new AssetImageRoute(components.assetImageRepository);
  const assetRoute = new AssetRoute(
    components.assetRepository,
    components.assetApi
  );
  const categoryRoute = new CategoryRoute(components.categoryRepository);
  const couponRoute = new CouponRoute(components.couponRepository);
  const ethereumConfigRoute = new EthereumConfigRoute(
    components.ethereumConfigRepository
  );
  const exchangeRateRoute = new ExchangeRateRoute(
    components.exchangeRateRepository
  );
  const externalWalletRoute = new ExternalWalletRoute(
    components.externalWalletRepository
  );
  const orderRoute = new OrderRoute(components.orderRepository);
  const rewardRoute = new RewardRoute(components.rewardRepository);
  const salesProposalRoute = new SalesProposalRoute(
    components.salesProposalRepository
  );
  const salesRoute = new SalesRoute(components.salesRepository);
  const tokenRoute = new TokenRoute(components.tokenRepository);
  const transactionRoute = new TransactionRoute(
    components.transactionRepository
  );
  const userRoute = new UserRoute(
    components.userRepository,
    components.userApi,
    components.userService,
    components.connectedAccountService
  );
  const walletRoute = new WalletRoute(components.walletRepository);
  const monthlyAssetInfoRoute = new MonthlyAssetInfoRoute(
    components.monthlyAssetInfoRepository
  );
  const authRoute = new AuthRoute(components.authApi);
  const migrationRoute = new MigrationRoute(
    components.userRepository,
    components.walletRepository,
    components.pocketRepository,
    components.externalWalletRepository,
    components.orderRepository,
    components.transactionRepository
  );
  const apiUserRoute = new ApiUserRoute(components.apiUserRepository);

  // _router.use('/api/v1/docs', swaggerRoute(express));
  _router.use('/api/v1/applied-coupons', appliedCouponRoute.handle());
  _router.use('/api/v1/asset-descs', assetDescRoute.handle());
  _router.use('/api/v1/asset-images', assetImageRoute.handle());
  _router.use('/api/v1/assets', assetRoute.handle());
  _router.use('/api/v1/auth', authRoute.handle());
  _router.use('/api/v1/categorys', categoryRoute.handle());
  _router.use('/api/v1/coupons', couponRoute.handle());
  _router.use('/api/v1/ethereum-configs', ethereumConfigRoute.handle());
  _router.use('/api/v1/exchange-rates', exchangeRateRoute.handle());
  _router.use('/api/v1/external-wallets', externalWalletRoute.handle());
  _router.use('/api/v1/orders', orderRoute.handle());
  _router.use('/api/v1/rewards', rewardRoute.handle());
  _router.use('/api/v1/sales-proposals', salesProposalRoute.handle());
  _router.use('/api/v1/saleses', salesRoute.handle());
  _router.use('/api/v1/tokens', tokenRoute.handle());
  _router.use('/api/v1/transactions', transactionRoute.handle());
  _router.use('/api/v1/users', userRoute.handle());
  _router.use('/api/v1/wallets', walletRoute.handle());
  _router.use('/api/v1/monthly-asset-infos', monthlyAssetInfoRoute.handle());
  _router.use('/api/v1/migration', migrationRoute.handle());
  _router.use('/api/v1/api-users', apiUserRoute.handle());

  return _router;
};
