import { ApiUser } from '@domain/api-user/api-user.model';
import { DestroyOptions, UpdateOptions } from 'sequelize';
import { Filter } from '@common/models/QueryOption';
import { ICountOptions } from 'sequelize-typescript';
import { removeDotInJson } from '@frontalnh/json-dot-parser';

export class ApiUserRepositoryImpl {
  constructor() {}

  async save(apiUser: ApiUser) {
    return await apiUser.save();
  }

  async findAll(filter: Filter) {
    if (!filter.raw) return await ApiUser.findAll(filter);

    let datas = await ApiUser.findAll(filter);
    for (let data of datas) {
      Object.assign(data, removeDotInJson(data));
    }

    return datas;
  }

  async findById(id: string) {
    let users = await ApiUser.findAll({ where: { id } });

    if (users.length === 0) return null;

    return users[0];
  }

  async getCount(filter: ICountOptions<ApiUser>) {
    return await ApiUser.count(filter);
  }

  async update(
    apiUser: Partial<ApiUser>,
    option: UpdateOptions
  ): Promise<[number, ApiUser[]]> {
    return await ApiUser.update(apiUser, option);
  }

  async delete(option: DestroyOptions): Promise<number> {
    return await ApiUser.destroy(option);
  }
}
