
  import { AssetImage } from '@domain/asset-image/asset-image.model';
  import { DestroyOptions, UpdateOptions } from 'sequelize';
  import { Filter } from '@common/models/QueryOption';
  import { ICountOptions } from 'sequelize-typescript';
  import { removeDotInJson } from '@frontalnh/json-dot-parser';

  export class AssetImageRepositoryImpl {
    constructor() {}
  
    async save(assetImage: AssetImage) {
      return await assetImage.save();
    }
  
    async findAll(filter: Filter) {
      if (!filter.raw) return await AssetImage.findAll(filter);

      let datas = await AssetImage.findAll(filter);
      for (let data of datas) {
        Object.assign(data, removeDotInJson(data));
      }
  
      return datas;
    }
  
    async findById(id: number) {
      return await AssetImage.findByPrimary(id);
    }

    async getCount(filter: ICountOptions<AssetImage>) {
      return await AssetImage.count(filter);
    }
  
    async update(
      assetImage: Partial<AssetImage>,
      option: UpdateOptions
    ): Promise<[number, AssetImage[]]> {
      return await AssetImage.update(assetImage, option);
    }
  
    async delete(option: DestroyOptions): Promise<number> {
      return await AssetImage.destroy(option);
    }
  }
  