import { Route } from 'server/common/models/Route';
import * as express from 'express';
import { httpSuccessResponse } from '@utils/httpSender';
import { SalesRepository } from '@domain/sales/sales.repository';
import { FilterSchema } from '@common/validateSchemas/FilterSchema';
import Joi from 'joi';
import { Sales } from '@domain/sales/sales.model';

export class SalesRoute implements Route {
  private router: express.Router;
  constructor(private salesRepository: SalesRepository) {
    this.salesRepository = salesRepository;
    this.router = express.Router();
  }
  handle() {
    this.router.post('', (...args) => this.create(...args));
    this.router.get('', (...args) => this.findAll(...args));
    this.router.get('/:id', (...args) => this.findById(...args));
    this.router.put('', (...args) => this.update(...args));
    this.router.delete('', (...args) => this.remove(...args));
    return this.router;
  }

  private async create(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let sales = new Sales(req.body);
      let created = await this.salesRepository.save(sales);
      return res.send(created);
    } catch (err) {
      return next(err);
    }
  }

  // @authGuard
  private async findAll(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let { value, error } = Joi.validate(req.query, FilterSchema, {
        convert: true
      });

      if (error) return next(error);

      let payload = await this.salesRepository.findAll(value);
      return httpSuccessResponse(res, { payload });
    } catch (err) {
      return next(err);
    }
  }

  private async findById(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let id = req.params.id;
      let sales = await this.salesRepository.findById(id);

      return res.send(sales);
    } catch (err) {
      return next(err);
    }
  }

  private async update(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let { sales, option } = req.body;
      let [count, payload] = await this.salesRepository.update(sales, option);

      return httpSuccessResponse(res, { payload, count });
    } catch (err) {
      return next(err);
    }
  }

  private async remove(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let option = req.body;
      let count = await this.salesRepository.delete(option);

      res.send(count.toString());
    } catch (err) {
      return next(err);
    }
  }
}
