
  import { Category } from '@domain/category/category.model';
  import { DestroyOptions, UpdateOptions } from 'sequelize';
  import { Filter } from '@common/models/QueryOption';
  import { ICountOptions } from 'sequelize-typescript';
  import { removeDotInJson } from '@frontalnh/json-dot-parser';

  export class CategoryRepositoryImpl {
    constructor() {}
  
    async save(category: Category) {
      return await category.save();
    }
  
    async findAll(filter: Filter) {
      if (!filter.raw) return await Category.findAll(filter);

      let datas = await Category.findAll(filter);
      for (let data of datas) {
        Object.assign(data, removeDotInJson(data));
      }
  
      return datas;
    }
  
    async findById(id: number) {
      return await Category.findByPrimary(id);
    }

    async getCount(filter: ICountOptions<Category>) {
      return await Category.count(filter);
    }
  
    async update(
      category: Partial<Category>,
      option: UpdateOptions
    ): Promise<[number, Category[]]> {
      return await Category.update(category, option);
    }
  
    async delete(option: DestroyOptions): Promise<number> {
      return await Category.destroy(option);
    }
  }
  