import {
  Table,
  Model,
  Column,
  DataType,
  PrimaryKey,
  AutoIncrement,
  ForeignKey,
  CreatedAt,
  UpdatedAt,
  BelongsTo,
  AllowNull,
  DefaultScope
} from 'sequelize-typescript';
import { Order } from '@domain/order/order.model';
import { Asset } from '@domain/asset/asset.model';
import { Sales } from '@domain/sales/sales.model';
import { User } from '@domain/user/user.model';

export enum RewardStatus {
  COMPLETED = 'COMPLETED',
  PENDING = 'PENDING'
}

/**
 *       .
 *           pending .
 *       completed  .
 *
 *   amount            amount   .
 */
@DefaultScope({
  include: [
    { model: () => Sales },
    { model: () => User, attributes: ['name', 'walletAddress'] },
    { model: () => Order }
  ]
})
@Table({ modelName: 'reward' })
export class Reward extends Model<Reward> {
  @PrimaryKey
  @AutoIncrement
  @Column({ type: DataType.INTEGER })
  id: Number;

  @Column({ type: DataType.ENUM({ values: Object.keys(RewardStatus) }) })
  status: RewardStatus;

  @AllowNull(false)
  @ForeignKey(() => User)
  @Column({ type: DataType.INTEGER })
  from: Number;

  @AllowNull(false)
  @ForeignKey(() => User)
  @Column({ type: DataType.INTEGER })
  to: Number;

  @AllowNull(false)
  @ForeignKey(() => Sales)
  @Column({ type: DataType.INTEGER })
  salesId: Number;

  @AllowNull(false)
  @ForeignKey(() => Asset)
  @Column({ type: DataType.INTEGER })
  assetId: Number;

  @AllowNull(false)
  @ForeignKey(() => Order)
  @Column({ type: DataType.INTEGER })
  orderId: Number;

  @Column({
    type: DataType.INTEGER,
    comment: ' BWX    .'
  })
  amount: Number;

  @Column({ type: DataType.INTEGER })
  currency: Number;

  @CreatedAt
  createdAt?: Date;

  @UpdatedAt
  updatedAt?: Date;

  @BelongsTo(() => Order)
  order: Order;

  @BelongsTo(() => Sales)
  sales: Sales;

  @BelongsTo(() => User)
  toUser: Partial<User>;
}
