import {
  Table,
  Model,
  Column,
  DataType,
  CreatedAt,
  UpdatedAt,
  PrimaryKey,
  AutoIncrement,
  AllowNull,
  ForeignKey
} from 'sequelize-typescript';
import { Asset } from '@domain/asset/asset.model';

@Table({ modelName: 'token' })
export class Token extends Model<Token> {
  @PrimaryKey
  @AutoIncrement
  @Column({ type: DataType.INTEGER })
  id: number;

  @Column({ type: DataType.STRING })
  type: string;

  @Column({ type: DataType.STRING })
  address: string;

  @ForeignKey(() => Asset)
  @Column({ type: DataType.INTEGER })
  assetId: number;

  @AllowNull(false)
  @Column({ type: DataType.STRING })
  name: string;

  @AllowNull(false)
  @Column({ type: DataType.STRING, comment: ' ' })
  symbol: string;

  @Column({ type: DataType.INTEGER })
  decimals: number;

  @Column({ type: DataType.INTEGER })
  totalSupply: number;

  @Column({ type: DataType.STRING })
  description?: string;

  @CreatedAt
  createdAt?: Date;

  @UpdatedAt
  updatedAt?: Date;
}
