import {
  Column,
  DataType,
  PrimaryKey,
  AutoIncrement,
  Table,
  CreatedAt,
  UpdatedAt,
  ForeignKey,
  Model
} from 'sequelize-typescript';
import { Sales } from '@domain/sales/sales.model';

@Table({ modelName: 'sales-proposal' })
export class SalesProposal extends Model<SalesProposal> {
  @PrimaryKey
  @AutoIncrement
  @Column({ type: DataType.INTEGER })
  id: Number;

  @Column({ type: DataType.INTEGER })
  @ForeignKey(() => Sales)
  salesId: Number;

  @Column({ type: DataType.STRING })
  lang: String;

  @Column({ type: DataType.STRING })
  title: String;

  @Column({ type: DataType.STRING })
  content: String;

  @CreatedAt
  createdAt?: Date;

  @UpdatedAt
  updatedAt?: Date;
}
