
  import { AppliedCoupon } from '@domain/applied-coupon/applied-coupon.model';
  import { DestroyOptions, UpdateOptions } from 'sequelize';
  import { Filter } from '@common/models/QueryOption';
  import { ICountOptions } from 'sequelize-typescript';
  import { removeDotInJson } from '@frontalnh/json-dot-parser';

  export class AppliedCouponRepositoryImpl {
    constructor() {}
  
    async save(appliedCoupon: AppliedCoupon) {
      return await appliedCoupon.save();
    }
  
    async findAll(filter: Filter) {
      if (!filter.raw) return await AppliedCoupon.findAll(filter);

      let datas = await AppliedCoupon.findAll(filter);
      for (let data of datas) {
        Object.assign(data, removeDotInJson(data));
      }
  
      return datas;
    }
  
    async findById(id: number) {
      return await AppliedCoupon.findByPrimary(id);
    }

    async getCount(filter: ICountOptions<AppliedCoupon>) {
      return await AppliedCoupon.count(filter);
    }
  
    async update(
      appliedCoupon: Partial<AppliedCoupon>,
      option: UpdateOptions
    ): Promise<[number, AppliedCoupon[]]> {
      return await AppliedCoupon.update(appliedCoupon, option);
    }
  
    async delete(option: DestroyOptions): Promise<number> {
      return await AppliedCoupon.destroy(option);
    }
  }
  