import {
  DataType,
  Column,
  Table,
  ForeignKey,
  Model,
  CreatedAt,
  UpdatedAt,
  PrimaryKey,
  AutoIncrement
} from 'sequelize-typescript';

@Table({ modelName: 'external-wallet' })
export class ExternalWallet extends Model<ExternalWallet> {
  @PrimaryKey
  @AutoIncrement
  @Column({
    type: DataType.INTEGER
  })
  id: number;

  @Column({
    type: DataType.STRING
  })
  name: string;

  @Column({ type: DataType.INTEGER })
  balance: number;

  @Column({ type: DataType.STRING })
  address: string;

  @Column({ type: DataType.STRING })
  description?: string;

  @CreatedAt
  createdAt?: Date;

  @UpdatedAt
  updatedAt?: Date;
}
