
  import { MonthlyAssetInfo } from '@domain/monthly-asset-info/monthly-asset-info.model';
  import { DestroyOptions, UpdateOptions } from 'sequelize';
  import { Filter } from '@common/models/QueryOption';
  import { ICountOptions } from 'sequelize-typescript';
  import { removeDotInJson } from '@frontalnh/json-dot-parser';

  export class MonthlyAssetInfoRepositoryImpl {
    constructor() {}
  
    async save(monthlyAssetInfo: MonthlyAssetInfo) {
      return await monthlyAssetInfo.save();
    }
  
    async findAll(filter: Filter) {
      if (!filter.raw) return await MonthlyAssetInfo.findAll(filter);

      let datas = await MonthlyAssetInfo.findAll(filter);
      for (let data of datas) {
        Object.assign(data, removeDotInJson(data));
      }
  
      return datas;
    }
  
    async findById(id: number) {
      return await MonthlyAssetInfo.findByPrimary(id);
    }

    async getCount(filter: ICountOptions<MonthlyAssetInfo>) {
      return await MonthlyAssetInfo.count(filter);
    }
  
    async update(
      monthlyAssetInfo: Partial<MonthlyAssetInfo>,
      option: UpdateOptions
    ): Promise<[number, MonthlyAssetInfo[]]> {
      return await MonthlyAssetInfo.update(monthlyAssetInfo, option);
    }
  
    async delete(option: DestroyOptions): Promise<number> {
      return await MonthlyAssetInfo.destroy(option);
    }
  }
  