
  import { Token } from '@domain/token/token.model';
  import { DestroyOptions, UpdateOptions } from 'sequelize';
  import { Filter } from '@common/models/QueryOption';
  import { ICountOptions } from 'sequelize-typescript';
  import { removeDotInJson } from '@frontalnh/json-dot-parser';

  export class TokenRepositoryImpl {
    constructor() {}
  
    async save(token: Token) {
      return await token.save();
    }
  
    async findAll(filter: Filter) {
      if (!filter.raw) return await Token.findAll(filter);

      let datas = await Token.findAll(filter);
      for (let data of datas) {
        Object.assign(data, removeDotInJson(data));
      }
  
      return datas;
    }
  
    async findById(id: number) {
      return await Token.findByPrimary(id);
    }

    async getCount(filter: ICountOptions<Token>) {
      return await Token.count(filter);
    }
  
    async update(
      token: Partial<Token>,
      option: UpdateOptions
    ): Promise<[number, Token[]]> {
      return await Token.update(token, option);
    }
  
    async delete(option: DestroyOptions): Promise<number> {
      return await Token.destroy(option);
    }
  }
  