
  import { Pocket } from '@domain/pocket/pocket.model';
  import { DestroyOptions, UpdateOptions } from 'sequelize';
  import { Filter } from '@common/models/QueryOption';
  import { ICountOptions } from 'sequelize-typescript';
  import { removeDotInJson } from '@frontalnh/json-dot-parser';

  export class PocketRepositoryImpl {
    constructor() {}
  
    async save(pocket: Pocket) {
      return await pocket.save();
    }
  
    async findAll(filter: Filter) {
      if (!filter.raw) return await Pocket.findAll(filter);

      let datas = await Pocket.findAll(filter);
      for (let data of datas) {
        Object.assign(data, removeDotInJson(data));
      }
  
      return datas;
    }
  
    async findById(id: number) {
      return await Pocket.findByPrimary(id);
    }

    async getCount(filter: ICountOptions<Pocket>) {
      return await Pocket.count(filter);
    }
  
    async update(
      pocket: Partial<Pocket>,
      option: UpdateOptions
    ): Promise<[number, Pocket[]]> {
      return await Pocket.update(pocket, option);
    }
  
    async delete(option: DestroyOptions): Promise<number> {
      return await Pocket.destroy(option);
    }
  }
  