import {
  Table,
  DataType,
  Column,
  PrimaryKey,
  AutoIncrement,
  CreatedAt,
  UpdatedAt,
  Model
} from 'sequelize-typescript';

@Table({ modelName: 'asset-desc' })
export class AssetDesc extends Model<AssetDesc> {
  @PrimaryKey
  @AutoIncrement
  @Column({
    type: DataType.INTEGER
  })
  id: Number;

  @Column({
    type: DataType.INTEGER
  })
  assetId: Number;

  @Column({ type: DataType.STRING })
  lang: String;

  @Column({
    type: DataType.STRING,
    comment: 'The description of asset'
  })
  assetDesc?: String;

  @Column({
    type: DataType.STRING
  })
  companyDesc?: String;

  @CreatedAt
  createdAt?: Date;

  @UpdatedAt
  updatedAt?: Date;
}
