import { PocketRepository } from '@domain/pocket/pocket.repository';
import { SelectedColumn, Pocket } from '@domain/pocket/pocket.model';
import { CustomError } from '@common/models/CustomError';
import { HttpErrCode } from '@common/constants/HttpErrCode';

export class PocketService {
  constructor(private pocketRepository: PocketRepository) {
    this.pocketRepository = pocketRepository;
  }
  async createPocketForWallet(userId: number, walletId: number) {
    let pockets = await this.pocketRepository.findAll({
      where: { userId, walletId }
    });

    if (pockets.length !== 0)
      throw new CustomError(
        HttpErrCode.CREATE_POCKET.ALREADY_EXIST,
        '     .'
      );

    let pocket = new Pocket();
    Object.assign(pocket, {
      selectedColumn: SelectedColumn.walletId,
      userId,
      walletId
    });

    return this.pocketRepository.save(pocket);
  }
}
