import { Route } from '../../common/models/Route';
import { Router } from 'express';
import { AuthApi } from '../../api/auth.api';
import Joi from 'joi';
import express from 'express';
import { LoginSchema } from '@common/validateSchemas/LoginSchema';

export class AuthRoute implements Route {
  router: Router;

  constructor(private authApi: AuthApi) {
    this.router = express.Router();
    this.authApi = authApi;
  }
  handle() {
    this.router.post('/login', this.login);
    this.router.post('/logout', this.logout);
    this.router.get('/verify-email', this.verifyEmail);

    return this.router;
  }

  private verifyEmail = async (
    req: express.Request,
    res: express.Response,
    next
  ) => {
    try {
      let { email, code } = req.query;

      let isAuthorized = await this.authApi.authenticateEmail(email, code);

      if (isAuthorized) {
        return res.redirect(
          'http://localhost:1234/email-verification-complete'
        );
      }
    } catch (err) {
      console.log(err);
      return next(err);
    }
  };

  private login = async (req, res, next) => {
    const body = req.body;

    let { error } = Joi.validate(body, LoginSchema);
    if (error) return next(error);

    const { email, password } = body;
    // login api
    try {
      const { user, token } = await this.authApi.login(email, password);

      res.cookie('jwtToken', token);
      res.send({ user, token });
    } catch (err) {
      return next(err);
    }
  };

  private logout = async (req, res) => {
    res.clearCookie('jwtToken');
    return res.json({ done: true });
  };
}
