import { Reward } from '@domain/reward/reward.model';
import { DestroyOptions, UpdateOptions } from 'sequelize';
import { Filter } from '@common/models/QueryOption';
import { ICountOptions } from 'sequelize-typescript';

export class RewardRepositoryImpl {
  constructor() {}

  async save(reward: Reward) {
    const _ = Reward.build(reward);
    return await _.save();
  }

  async findAll(filter: Filter) {
    return await Reward.findAll(filter);
  }

  async findById(id: number) {
    return await Reward.findByPrimary(id);
  }

  async getCount(filter: ICountOptions<Reward>) {
    return await Reward.count(filter);
  }

  async update(
    reward: Partial<Reward>,
    option: UpdateOptions
  ): Promise<[number, Reward[]]> {
    return await Reward.update(reward, option);
  }

  async delete(option: DestroyOptions): Promise<number> {
    return await Reward.destroy(option);
  }
}
