import { ConnectedAccount } from '@domain/connected-account/connected-account.model';
import { DestroyOptions, UpdateOptions } from 'sequelize';
import { Filter } from '@common/models/QueryOption';
import { ICountOptions } from 'sequelize-typescript';
import { removeDotInJson } from '@frontalnh/json-dot-parser';

export class ConnectedAccountRepositoryImpl {
  constructor() {}

  async save(connectedAccount: ConnectedAccount) {
    return await connectedAccount.save();
  }

  async findAll(filter: Filter) {
    if (!filter.raw) return await ConnectedAccount.findAll(filter);

    let datas = await ConnectedAccount.findAll(filter);
    for (let data of datas) {
      Object.assign(data, removeDotInJson(data));
    }

    return datas;
  }

  async findById(id: number) {
    return await ConnectedAccount.findByPrimary(id);
  }

  async getCount(filter: ICountOptions<ConnectedAccount>) {
    return await ConnectedAccount.count(filter);
  }

  async update(
    connectedAccount: Partial<ConnectedAccount>,
    option: UpdateOptions
  ): Promise<[number, ConnectedAccount[]]> {
    return await ConnectedAccount.update(connectedAccount, option);
  }

  async delete(option: DestroyOptions): Promise<number> {
    return await ConnectedAccount.destroy(option);
  }
}
