import { AppliedCoupon } from '../applied-coupon/applied-coupon.model';
import {
  Table,
  PrimaryKey,
  AutoIncrement,
  Column,
  DataType,
  ForeignKey,
  HasMany,
  Model,
  CreatedAt,
  UpdatedAt,
  DefaultScope
} from 'sequelize-typescript';
import { User } from '@domain/user/user.model';
import { Sales } from '@domain/sales/sales.model';

export enum OrderStatus {
  WAIT = 'WAIT',
  COMPLETE = 'COMPLETE',
  EXPIRED = 'EXPIRED'
}

@DefaultScope({ include: [() => AppliedCoupon] })
@Table({ modelName: 'order', underscored: true })
export class Order extends Model<Order> {
  @PrimaryKey
  @AutoIncrement
  @Column(DataType.INTEGER)
  id: Number;

  @ForeignKey(() => User)
  @Column(DataType.INTEGER)
  userId: Number;

  @ForeignKey(() => Sales)
  @Column(DataType.INTEGER)
  salesId: Number;

  @Column({
    type: DataType.ENUM({ values: Object.keys(OrderStatus) }),
    comment: ' '
  })
  status?: OrderStatus;

  @Column({
    type: DataType.STRING
  })
  hash: String;

  @Column({
    type: DataType.INTEGER,
    comment:
      '        .'
  })
  purchasePrice: Number;

  @Column({
    type: DataType.INTEGER
  })
  tokenAmount: Number;

  @Column({
    type: DataType.STRING
  })
  purchaseCurrency: String;

  @Column({
    type: DataType.INTEGER
  })
  tokenId: Number;

  @CreatedAt
  createdAt: Date;

  @UpdatedAt
  updatedAt: Date;

  @HasMany(() => AppliedCoupon)
  appliedCoupons: AppliedCoupon[];
}
