import { UserRepository } from '@domain/user/user.repository';
import { UserStatus, User } from '@domain/user/user.model';
import { CustomError } from '@common/models/CustomError';
import { PocketService } from '@domain/pocket/pocket.service';
import { HttpErrCode } from '@common/constants/HttpErrCode';
import { WalletService } from '@domain/wallet/wallet.service';
import { createUnzip } from 'zlib';

export class UserService {
  constructor(
    private userRepository: UserRepository,
    private pocketService: PocketService,
    private walletService: WalletService
  ) {
    (this.userRepository = userRepository),
      (this.pocketService = pocketService),
      (this.walletService = walletService);
  }

  /**
   *       .
   *
   * @param phone   
   * @param firstName  (      firstName .)
   * @param birthday   1992-04-30    .
   */
  async createUser(
    phone: string,
    firstName: string,
    birthday: string,
    status: UserStatus
  ) {
    console.log('found users', phone, firstName, birthday, status);

    let users = await this.userRepository.findAll({
      where: { phone, birthday, status: UserStatus.ACTIVE }
    });

    if (users.length !== 0)
      throw new CustomError(
        HttpErrCode.REGISTER.EXIST_PHONE,
        '     .'
      );

    let user = new User({ phone, birthday, firstName, status });
    let createdUser = await this.userRepository.save(user);

    let wallet = await this.walletService.popWallet();
    await this.pocketService.createPocketForWallet(createdUser.id, wallet.id);

    let result = await this.userRepository.findById(createdUser.id);

    return result;
  }
}
