import {
  Table,
  PrimaryKey,
  AutoIncrement,
  Column,
  DataType,
  Model,
  Default,
  CreatedAt,
  UpdatedAt,
  DefaultScope,
  HasMany,
  AllowNull
} from 'sequelize-typescript';
import * as bcrypt from 'bcryptjs';
import { ConnectedAccount } from '@domain/connected-account/connected-account.model';
import { Pocket } from '@domain/pocket/pocket.model';

export enum UserPermission {
  ADMIN = 'ADMIN',
  GENERAL = 'GENERAL',
  SELLER = 'SELLER'
}

export enum UserStatus {
  ACTIVE = 'ACTIVE',
  INACTIVE = 'INACTIVE',
  UNAUTHORIZED = 'UNAUTHORIZED'
}

export enum KYCStatus {
  VALID = 'VALID',
  INVALID = 'INVALID'
}

@DefaultScope({
  order: [['createdAt', 'DESC']],
  include: [{ model: () => ConnectedAccount }, { model: () => Pocket }]
})
@Table({ modelName: 'user', underscored: true })
export class User extends Model<User> {
  @PrimaryKey
  @AutoIncrement
  @Column({
    type: DataType.INTEGER
  })
  id: number;

  @AllowNull(false)
  @Default(UserPermission.GENERAL)
  @Column({
    type: DataType.STRING,
    values: Object.keys(UserPermission)
  })
  permission?: string;

  @Default(UserStatus.UNAUTHORIZED)
  @Column({ type: DataType.ENUM({ values: Object.keys(UserStatus) }) })
  status: UserStatus;

  @Column({
    type: DataType.STRING
  })
  email?: string;

  @AllowNull(false)
  @Column({
    type: DataType.STRING,
    comment: '   "+82 01012341234"    .'
  })
  phone: string;

  @Column({
    type: DataType.STRING,
    comment: '  1992-04-30    .'
  })
  birthday: string;

  @Column({
    type: DataType.STRING
  })
  password?: string;

  @Column({
    type: DataType.STRING
  })
  firstName: string;

  @Column({
    type: DataType.STRING
  })
  lastName: string;

  @Column({
    type: DataType.STRING,
    values: Object.keys(KYCStatus)
  })
  KYCStatus?: KYCStatus;

  @Column({
    type: DataType.STRING,
    comment: ' otp  otp   '
  })
  OAuthKey?: string;

  @Column({
    type: DataType.STRING,
    comment: '  '
  })
  emailVerificationCode?: string;

  @CreatedAt
  createdAt?: Date;

  @UpdatedAt
  updatedAt?: Date;

  @HasMany(() => ConnectedAccount)
  connectedAccounts: ConnectedAccount[];

  @HasMany(() => Pocket)
  pockets: Pocket[];

  public comparePassword(password: string): boolean {
    return bcrypt.compareSync(password, this.password);
  }

  public setEmail(email: string) {
    this.email = email;
  }

  public setPassword(password: string) {
    this.password = password;
  }
}
