import {
  Table,
  Model,
  DataType,
  Column,
  PrimaryKey,
  AutoIncrement,
  BelongsTo,
  ForeignKey,
  HasOne,
  DefaultScope,
  AllowNull,
  CreatedAt,
  UpdatedAt
} from 'sequelize-typescript';
import { User } from '@domain/user/user.model';
import { Token } from '@domain/token/token.model';

export enum AssetType {
  FUNCTIONAL = 'FUNCTIONAL',
  INTEREST = 'INTEREST'
}

export enum Country {
  KR = 'KR'
}

@DefaultScope({ include: [{ model: () => Token }] })
@Table({ modelName: 'asset' })
export class Asset extends Model<Asset> {
  @PrimaryKey
  @AutoIncrement
  @Column({ type: DataType.INTEGER })
  id: number;

  @AllowNull(false)
  @ForeignKey(() => User)
  @Column({ type: DataType.INTEGER, comment: '  ' })
  userId: number;

  @AllowNull(false)
  @ForeignKey(() => Token)
  @Column({ type: DataType.INTEGER, comment: '   ' })
  tokenId: number;

  @Column({ type: DataType.STRING })
  name: String;

  @Column({ type: DataType.STRING })
  type: String;

  @AllowNull(false)
  @Column({ type: DataType.INTEGER, comment: '   ex) 4' })
  allocationDate: number;

  @Column({ type: DataType.STRING })
  country: String;

  @Column({ type: DataType.STRING })
  address: String;

  @Column({ type: DataType.INTEGER })
  minInterest: number;

  @Column({ type: DataType.INTEGER })
  maxInterest: number;

  @Column({ type: DataType.INTEGER, comment: '  ' })
  minInvestor: number;

  @Column({ type: DataType.INTEGER, comment: '  ' })
  maxInvestor: number;

  @Column({ type: DataType.INTEGER })
  totalSoftCap: number;

  @Column({ type: DataType.INTEGER })
  totalHardCap: number;

  @Column({ type: DataType.DATE })
  startAt: Date;

  @Column({ type: DataType.DATE })
  endAt: Date;

  @Column({ type: DataType.STRING, comment: '   ' })
  coverImgPath: string;

  @CreatedAt
  createdAt?: Date;

  @UpdatedAt
  updatedAt?: Date;

  @BelongsTo(() => User)
  user: User;

  @HasOne(() => Token)
  token: Token;

  totalInvestAmount: number;

  totalThisMonthRevenue: number;

  public setTotalInvestAmount(amount) {
    this.totalInvestAmount = amount;
  }
  public setTotalThisMonthRevenue(amount) {
    this.totalThisMonthRevenue = amount;
  }
}
