import { Route } from 'server/common/models/Route';
import * as express from 'express';
import { httpSuccessResponse } from '@utils/httpSender';
import { UserRepository } from '@domain/user/user.repository';
import Joi from 'joi';
import { CreateUserSchema } from '@common/validateSchemas/CreateUserSchema';
import { User, UserStatus } from '@domain/user/user.model';
import { UserApi } from '@api/user.api';
import { authGuard } from '@infra/guard/auth.guard';
import { CreateSplashUserSchema } from '@common/validateSchemas/CreateSplashUserSchema';
import { ApiUser } from '@domain/api-user/api-user.model';
import { UserService } from '@domain/user/user.service';
import { ConnectedAccountService } from '@domain/connected-account/connected-account.service';
import { apiGuard } from '@infra/guard/api.guard';
import { logger } from '@utils/logger';

export class UserRoute implements Route {
  private router: express.Router;
  constructor(
    private userRepository: UserRepository,
    private userApi: UserApi,
    private userService: UserService,
    private connectedAccountService: ConnectedAccountService
  ) {
    this.userRepository = userRepository;
    this.userApi = userApi;
    this.userService = userService;
    this.connectedAccountService = connectedAccountService;
    this.router = express.Router();
  }
  handle() {
    /**
     * @swagger
     * /users/splash:
     *   post:
     *     description:  .
     *     tags:
     *       - Users
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: body
     *         name: body
     *         type: object
     *         description:   .
     *         schema:
     *           $ref: '#/definitions/CreateSplashUserSchema'
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           $ref: '#/definitions/ExtendedUser'
     */
    this.router.post('/splash', (...args) => this.createSplashUser(...args));
    /**
     * @swagger
     * /users:
     *   post:
     *     description:  .
     *     tags:
     *       - Users
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: body
     *         name: body
     *         type: object
     *         description:    
     *         schema:
     *           $ref: '#/definitions/CreateUserSchema'
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           $ref: '#/definitions/User'
     */
    this.router.post('', (...args) => this.create(...args));

    /**
     * @swagger
     * /users/me:
     *   get:
     *     description:    .
     *     tags:
     *       - Users
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: cookie
     *         name: jwtToken
     *         type: string
     *         description: jwt 
     *     responses:
     *       200:
     *         description:   
     *         schema:
     *           $ref: '#/definitions/User'
     */
    this.router.post('/me', (...args) => this.getMyInfo(...args));

    /**
     * @swagger
     * /users:
     *   get:
     *     description: User API
     *     tags:
     *       - Users
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: query
     *         name: where
     *         type: object
     *         description: Where clause explains about the constraints used in find data
     *       - in: query
     *         name: limit
     *         type: integer
     *         description: How many data do you want?
     *       - in: query
     *         name: offset
     *         type: integer
     *         description: Where to start find data
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           type: object
     *           properties:
     *             payload:
     *               type: array
     *               items:
     *                 $ref: '#/definitions/User'
     */
    this.router.get('', (...args) => this.findAll(...args));
    /**
     * @swagger
     * /users/{id}:
     *   get:
     *     description: User API
     *     tags:
     *       - Users
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: path
     *         name: id
     *         type: number
     *         description: Single user
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           $ref: '#/definitions/User'
     */
    this.router.get('/:id', (...args) => this.findById(...args));
    /**
     * @swagger
     * /users:
     *   put:
     *     description: Update user
     *     tags:
     *       - Users
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: body
     *         name: body
     *         schema:
     *           properties:
     *             data:
     *               $ref: '#/definitions/User'
     *             option:
     *               type: object
     *               schema:
     *                 type: object
     *                 properties:
     *                   where:
     *                     type: string
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           $ref: '#/definitions/User'
     */
    this.router.put('', (...args) => this.update(...args));
    /**
     * @swagger
     * /users:
     *   delete:
     *     description: Delete user
     *     tags:
     *       - Users
     *     produces:
     *       - application/json
     *     parameters:
     *       - in: body
     *         name: body
     *         schema:
     *           $ref: '#/definitions/User'
     *     responses:
     *       200:
     *         description: Success
     *         schema:
     *           properties:
     *             count:
     *               type: integer
     */
    this.router.delete('', (...args) => this.remove(...args));
    return this.router;
  }

  @apiGuard
  private async createSplashUser(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction,
    user?: ApiUser
  ) {
    try {
      logger.info('Register Splash User');
      let { error } = Joi.validate(req.body, CreateSplashUserSchema);
      if (error) return next(error);

      let { phone, firstName, birthday } = req.body;

      let createdUser = await this.userService.createUser(
        phone,
        firstName,
        birthday,
        UserStatus.ACTIVE
      );

      let connectedAccount = await this.connectedAccountService.createSplashAccount(
        createdUser.id,
        createdUser.phone
      );

      let result = await this.userRepository.findById(createdUser.id);

      return res.send(result);
    } catch (err) {
      next(err);
    }
  }

  @authGuard
  private async getMyInfo(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction,
    user?: User
  ) {
    return res.send(user);
  }

  private async create(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let { error } = Joi.validate(req.body, CreateUserSchema);
      if (error) return next(error);

      let { email, password, name, phone, birthday } = req.body;

      let created = await this.userApi.register(
        email,
        name,
        password,
        phone,
        birthday
      );

      return res.send(created);
    } catch (err) {
      return next(err);
    }
  }

  // @authGuard
  private async findAll(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let users = await this.userRepository.findAll(req.query);
      let payload = users;
      return httpSuccessResponse(res, { payload });
    } catch (err) {
      return next(err);
    }
  }

  private async findById(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let id = req.params.id;
      let user = await this.userRepository.findById(id);

      return res.send(user);
    } catch (err) {
      return next(err);
    }
  }

  private async update(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let { data, option } = req.body;
      let [count, userList] = await this.userRepository.update(data, option);

      let payload = userList;
      return httpSuccessResponse(res, { payload, count });
    } catch (err) {
      return next(err);
    }
  }

  private async remove(
    req: express.Request,
    res: express.Response,
    next: express.NextFunction
  ) {
    try {
      let option = req.body;
      let count = await this.userRepository.delete(option);

      res.send(count.toString());
    } catch (err) {
      return next(err);
    }
  }
}
