import {
  Table,
  Model,
  DataType,
  Column,
  PrimaryKey,
  AutoIncrement
} from 'sequelize-typescript';

@Table({ modelName: 'category' })
export class Category extends Model<Category> {
  @PrimaryKey
  @AutoIncrement
  @Column({ type: DataType.INTEGER })
  id: Number;

  @Column({ type: DataType.STRING })
  depth1: String;

  @Column({ type: DataType.STRING })
  depth2: String;

  @Column({ type: DataType.STRING })
  depth3: String;

  @Column({ type: DataType.STRING })
  name: String;

  @Column({ type: DataType.DATE })
  createdAt?: Date;

  @Column({ type: DataType.DATE })
  updatedAt?: Date;
}
