import {
  Table,
  Model,
  DataType,
  Column,
  PrimaryKey,
  AutoIncrement,
  UpdatedAt,
  CreatedAt
} from 'sequelize-typescript';

@Table({ modelName: 'asset-image' })
export class AssetImage extends Model<AssetImage> {
  @PrimaryKey
  @AutoIncrement
  @Column({ type: DataType.INTEGER })
  id: Number;

  @Column({ type: DataType.INTEGER })
  assetId: Number;

  @Column({ type: DataType.STRING })
  name?: String;

  @Column({ type: DataType.STRING })
  path: String;

  @Column({ type: DataType.STRING })
  description?: String;

  @CreatedAt
  createdAt?: Date;

  @UpdatedAt
  updatedAt?: Date;
}
