import { Asset } from '@domain/asset/asset.model';
import { DestroyOptions, UpdateOptions } from 'sequelize';
import { Filter } from '@common/models/QueryOption';
import { removeDotInJson } from '@frontalnh/json-dot-parser';

export class AssetRepositoryImpl {
  constructor() {}

  async save(asset: Asset) {
    const _ = Asset.build(asset);
    return await _.save();
  }

  async findAll(filter: Filter) {
    if (!filter.raw) return await Asset.findAll(filter);

    let datas = await Asset.findAll(filter);
    for (let data of datas) {
      removeDotInJson(data);
    }

    return datas;
  }

  async findById(id: number) {
    return await Asset.findByPrimary(id);
  }

  async update(
    asset: Partial<Asset>,
    option: UpdateOptions
  ): Promise<[number, Asset[]]> {
    return await Asset.update(asset, option);
  }

  async delete(option: DestroyOptions): Promise<number> {
    return await Asset.destroy(option);
  }
}
