
  import { SalesProposal } from '@domain/sales-proposal/sales-proposal.model';
  import { DestroyOptions, UpdateOptions } from 'sequelize';
  import { Filter } from '@common/models/QueryOption';
  import { ICountOptions } from 'sequelize-typescript';
  import { removeDotInJson } from '@frontalnh/json-dot-parser';

  export class SalesProposalRepositoryImpl {
    constructor() {}
  
    async save(salesProposal: SalesProposal) {
      return await salesProposal.save();
    }
  
    async findAll(filter: Filter) {
      if (!filter.raw) return await SalesProposal.findAll(filter);

      let datas = await SalesProposal.findAll(filter);
      for (let data of datas) {
        Object.assign(data, removeDotInJson(data));
      }
  
      return datas;
    }
  
    async findById(id: number) {
      return await SalesProposal.findByPrimary(id);
    }

    async getCount(filter: ICountOptions<SalesProposal>) {
      return await SalesProposal.count(filter);
    }
  
    async update(
      salesProposal: Partial<SalesProposal>,
      option: UpdateOptions
    ): Promise<[number, SalesProposal[]]> {
      return await SalesProposal.update(salesProposal, option);
    }
  
    async delete(option: DestroyOptions): Promise<number> {
      return await SalesProposal.destroy(option);
    }
  }
  