
  import { ExchangeRate } from '@domain/exchange-rate/exchange-rate.model';
  import { DestroyOptions, UpdateOptions } from 'sequelize';
  import { Filter } from '@common/models/QueryOption';
  import { ICountOptions } from 'sequelize-typescript';
  import { removeDotInJson } from '@frontalnh/json-dot-parser';

  export class ExchangeRateRepositoryImpl {
    constructor() {}
  
    async save(exchangeRate: ExchangeRate) {
      return await exchangeRate.save();
    }
  
    async findAll(filter: Filter) {
      if (!filter.raw) return await ExchangeRate.findAll(filter);

      let datas = await ExchangeRate.findAll(filter);
      for (let data of datas) {
        Object.assign(data, removeDotInJson(data));
      }
  
      return datas;
    }
  
    async findById(id: number) {
      return await ExchangeRate.findByPrimary(id);
    }

    async getCount(filter: ICountOptions<ExchangeRate>) {
      return await ExchangeRate.count(filter);
    }
  
    async update(
      exchangeRate: Partial<ExchangeRate>,
      option: UpdateOptions
    ): Promise<[number, ExchangeRate[]]> {
      return await ExchangeRate.update(exchangeRate, option);
    }
  
    async delete(option: DestroyOptions): Promise<number> {
      return await ExchangeRate.destroy(option);
    }
  }
  